.lead-capture {
  .inactive-school {
    display: none;
  }

  .col-lg-6 {
    padding: 15px;
  }

  img {
    display: block;
    margin: 0 auto;
  }

  iframe {
    width: 100%;
    border: none;
  }

  .school-location-label {
    font-weight: bold;
  }

  #school-location {
    color: #2b2b2b;
    background-color: #fff;
    padding: 5px;
  }
}