// Styling for wildcards section of advanced custom fields//

section.wildcards {
  // Block Gradient Styles
  @include block-gradient-border(
    var(--wildcards-block-gradient-border),
    var(--wildcards-padding-top),
    var(--wildcards-padding-bottom)
  );

  .wildcards-container {
    padding: 0;
    &.has-heading-text-shadow {
      .wildcard-content {
        h3 {
          text-shadow: var(--wildcards-heading-text-shadow);
        }
      }
    }
  }

  .wildcard {
    height: calc(var(--wildcard-height) / 1.5);
    position: relative;
    overflow: hidden;
    @media (min-width: $break-large) {
      height: var(--wildcard-height);
    }
    &.col-md {
      max-height: 500px;
    }

    &:hover,
    &:active {
      .wildcard-image img {
        transform: scale(1.2);
      }
    }

    .wildcard-container {
      width: 100%;
      height: 100%;
      max-height: 100%;
      position: absolute;
      .wildcard-image {
        overflow: hidden;
        height: 100%;

        img {
          height: 100%;
          width: 100%;
          transition: transform 0.5s;
        }
      }

      .wildcard-image-overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
      }

      .wildcard-content {
        position: absolute;
        z-index: 200;
        bottom: 0;
        color: #ffffff;
        padding: 2rem;
        &.has-card-font-styles {
          h3 {
            font-family: var(--wildcard-card-font-family);
            font-size: var(--wildcard-card-font-size);
            font-weight: var(--wildcard-card-font-weight);
            text-transform: var(--wildcard-card-text-transform);
            letter-spacing: var(--wildcard-card-letter-spacing);
          }
        }
        h3 {
          text-transform: uppercase;
          color: var(--wp--preset--color--white);
        }
      }

      .wildcard-link {
        position: absolute;
        z-index: 300;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
  }
}
