section.testimonial-cards {
  width: 100%;
  overflow: hidden;
  padding: 0;

  // Block Gradient Styles

  @include block-gradient-border(
    var(--testimonial-cards-block-gradient-border),
    var(--testimonial-cards-padding-top),
    var(--testimonial-cards-padding-bottom)
  );

  // .row.testimonials-container {
  //   margin-right: 0;
  //   margin-left: 0;
  // }

  .testimonial-content-column,
  .testimonial-carousel-column {
    padding: 0;
  }

  .testimonial-content-column {
    position: relative;
    padding: 0;
  }

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .carousel-item {
    padding: 0 10%;
    .testimonial-card-wrapper {
      max-width: 680px;
      height: 100%;
      margin: 0 auto;
    }
  }

  // Block styles
  .testimonial-info {
    width: 80%;
    margin: auto;
    position: relative;
    max-width: 1400px;
    // .testimonial-title {
    //   text-transform: uppercase;
    // }
    h3,
    p {
      width: 100%;
    }
  }

  // Carousel layout by screen size
  .testimonial-info {
    width: calc(80% + $theme-margin);
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 1fr 4fr 1fr;
    margin: 0 auto;
    padding: 0;
    @media (min-width: $break-medium) {
      width: 100%;
      grid-template-columns: 10% auto 10%;
    }
    @media (min-width: $break-large) {
      width: auto;
      height: 100%;
      max-width: 850px;
    }
    .testimonial-heading-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      grid-area: 1 / 1 / 2 / 4;
      padding: 0;
      @media (min-width: $break-medium) {
        padding: 0 10%;
      }
      @media (min-width: $break-large) {
        grid-area: 1 / 2 / 2 / 3;
        padding: 0;
      }
    }
  }

  .testimonial-cards-track {
    width: 100%;
    display: grid;
    grid-template-rows: auto 75px;
    grid-template-columns: 1fr 4fr 1fr;
    margin: 0 auto;
    padding: 0;
    @media (min-width: $break-medium) {
      width: 100%;
      grid-template-rows: auto 50px;
      grid-template-columns: 10% auto 10%;
    }
    @media (min-width: $break-large) {
      width: auto;
    }
    .carousel-indicators {
      grid-area: 2 / 2 / 3 / 3;
    }
    .testimonial-cards-container.carousel-inner {
      width: 100%;
      grid-area: 1 / 1 / 2 / 4;
      margin: 0 auto;
    }
    .carousel-control-prev {
      width: 100%;
      grid-area: 2 / 2 / 3 / 3;
    }
    .carousel-control-next {
      width: 100%;
      grid-area: 2 / 2 / 3 / 3;
    }
  }

  // Testimonial card design
  .testimonial-card {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: $theme-padding 0;
    color: var(--testimonial-card-text-color);

    .testimonial-card-quote {
      width: 30px;
      height: 30px;
      margin: 0 0.5rem 0 0;
      position: relative;
      top: 35px;
      left: 15px;
      @media (min-width: $break-xlarge) {
        width: 45px;
        height: 45px;
        top: 40px;
        left: 0;
        margin: 0 $half-margin 0 0;
      }
      i::before {
        font-size: 30px;
        @media (min-width: $break-large) {
          font-size: 45px;
        }
      }
    }

    .testimonial-card-review {
      p {
        padding: $half-padding $half-padding $half-padding $theme-padding;
        font-size: 1.2rem;
        text-indent: 25px;
        @media (min-width: $break-xlarge) {
          font-size: 1.4rem;
        }
      }
    }

    .testimonial-card-name {
      display: flex;
      justify-content: center;
      p {
        font-size: 1rem;
        line-height: 1.2rem;
        @media (min-width: $break-xlarge) {
          font-size: 1.3rem;
        }
      }
      .review-link {
        color: var(--wp--preset--color--brand-color);
        &.has-link-text-color {
          color: var(--header-nav-link-text-color);
        }
        &.has-link-text-hover-color {
          &:hover {
            color: var(--header-nav-link-text-hover-color);
          }
        }
        p {
          line-height: 1.2rem;
          font-size: 1rem;
          margin-left: 0.5rem;
          @media (min-width: $break-xlarge) {
            font-size: 1.3rem;
          }
        }
      }
    }
  }

  .testimonial-card-source {
    img {
      width: 50px;
      height: 50px;
    }
  }

  // carousel nav control styles
  .carousel-indicators {
    button {
      width: 15px;
      height: 15px;
      margin: 0 10px;
      border-radius: 0;
      background-color: rgba(0, 0, 0, 0.5);
      &.active {
        background-color: var(--testimonial-card-text-color);
      }
    }
  }

  .carousel-control-next,
  .carousel-control-prev {
    opacity: 1;
    span {
      width: 35px;
      height: 35px;
      @media (min-width: $break-small) {
        width: 50px;
        height: 50px;
      }
    }
    i {
      font-size: 2rem;
      color: var(--testimonial-card-text-color);
      opacity: 0.7;
    }
  }
  .carousel-control-next {
    span {
      margin-right: $half-margin;
      margin-left: auto;
    }
  }
  .carousel-control-prev {
    span {
      margin-left: $half-margin;
      margin-right: auto;
    }
  }

  .testimonial-heading-container {
    h3 {
      &.has-font-size {
        font-size: var(--testimonial-cards-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--testimonial-cards-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--testimonial-cards-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--testimonial-cards-heading-custom-word-spacing);
      }
    }
  }
}
