.carousel-card {
  .carousel-card-image-container {
    height: auto;
    padding: 0;
    position: relative;
  }

  .carousel-card-image {
    width: 100%;
    height: 315px;
    position: relative;
    @media (min-width: $break-large) {
      height: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
    }
  }

  .carousel-card-content-container {
    background-color: var(--wp--preset--color--base-2);
    .carousel-card-subheading {
      &.has-subheader-override-color {
        h5 {
          color: var(--carousel-subheading-custom-color);
        }
      }
    }
    .carousel-card-heading {
      &.has-header-override-color {
        h4 {
          color: var(--carousel-heading-custom-color);
        }
      }
    }
    .carousel-card-content {
      padding: 1rem;
      @media (min-width: $break-large) {
        padding: 0;
      }
    }
  }

  .carousel-info {
    h4 {
      &.has-font-size {
        font-size: var(--carousel-item-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--carousel-item-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--carousel-item-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--carousel-item-heading-custom-word-spacing);
      }
    }
    h5 {
      &.has-font-size {
        font-size: var(--carousel-item-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--carousel-item-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--carousel-item-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--carousel-item-subheading-custom-word-spacing);
      }
    }
  }
}
