section.contact-us {
  width: 100%;
  padding: 0;
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--contact-us-block-gradient-border),
    var(--contact-us-padding-top),
    var(--contact-us-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .contact-us-navigation-content,
  .contact-us-location {
    max-width: 100%;
    margin: 1rem 5%;
    @media (min-width: $break-small) {
      max-width: 95%;
      margin: 0 auto;
    }
    @media (min-width: $break-xxl) {
      max-width: 1550px;
    }
  }

  .contact-us-container {
    position: relative;
    .contact-us-navigation-content {
      &.has-gradient-card-border {
        background-image: var(--contact-us-card-gradient-border);
        padding: var(--contact-us-card-gradient-border-size);
      }
    }
  }

  .contact-us-navigation-container {
    padding: 4rem 2rem;
    background-color: var(--wp--preset--color--base-2);
    @media (min-width: $break-large) {
      padding: 4rem;
    }
  }

  .contact-us-navigation-content {
    margin-bottom: 40px;
    flex-direction: column-reverse;
    @media (min-width: $break-large) {
      flex-direction: row;
    }
    .card-gradient-border {
      display: flex;
      flex-direction: column-reverse;
      @media (min-width: $break-large) {
        flex-direction: row;
      }
    }
    .card-gradient-border {
      background-image: var(--contact-us-card-gradient-border);
      padding: var(--contact-us-card-gradient-border-size);
    }
  }

  .contact-us-nav-image-container {
    padding: 0;

    .contact-us-image {
      position: relative;
      height: 100%;
      @media (min-width: $break-large) {
        min-height: 300px;
      }
      &::after {
        content: "";
        background-color: var(--contact-us-card-image-overlay);
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        position: absolute;
      }
    }
    img {
      width: 100%;
      height: 100%;
      position: absolute;
    }
  }

  .contact-us-header {
    padding: 0 0.5rem;
  }

  .contact-us-nav.container {
    max-width: 100%;
    padding: 0;

    // Applies the custom active location button color styles
    &.has-active-card-text {
      .button-school-nav-link {
        &.active {
          color: var(--button-contact-us-active-text-color);
          h1 {
            color: var(--button-contact-us-active-text-color);
          }
          i {
            color: var(--button-contact-us-active-text-color);
          }
        }
      }
    }
    &.has-active-card-background {
      .button-school-nav-link {
        &.active {
          background-color: var(--button-contact-us-active-background-color);
          border-color: var(--button-contact-us-active-background-color);
        }
      }
    }

    .nav-item {
      margin-bottom: 10px;
      padding: 0 0.5rem;
      @media (min-width: $break-large) {
        margin: 0.5rem 0;
        padding: 0 0.5rem;
      }
    }

    .button-school-nav-link {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      border: 2px solid #aaa;
      border-radius: 0;
      text-align: left;
      padding: 1rem;
      color: #1b1b1b;
      background-color: transparent;
      &.active {
        background-color: var(--wp--preset--color--brand-color);
        border: 2px solid var(--wp--preset--color--brand-color);
        border-radius: 0;
        color: var(--wp--preset--color--primary-text);
        h1 {
          color: var(--wp--preset--color--primary-text);
        }
      }

      .button-contact-us-school-name {
        font-size: 1rem;
        margin: 0;
        @media (min-width: $break-large) {
          margin: 0 0 10px 0;
        }
      }

      .button-contact-us-school-info {
        font-weight: 400;
        font-size: 0.9rem;
      }

      .button-contact-us-address,
      .button-contact-us-phone-number,
      .button-contact-us-email {
        display: none;
        margin-bottom: 0.3rem;
        @media (min-width: $break-large) {
          display: flex;
        }
        i.fas {
          width: 1.2rem;
        }
      }

      .button-contact-us-email {
        word-break: anywhere;
      }
    }
  }

  .contact-us-location {
    background-color: #ededed;
    .tab-content {
      &.has-gradient-card-border {
        background-image: var(--contact-us-card-gradient-border);
        padding: var(--contact-us-card-gradient-border-size);
      }
    }

    .location-contact-us-container {
      display: grid;
      grid-template-columns: auto;
      grid-template-rows: repeat(3, auto);
      padding: 4rem 2rem;
      background-color: var(--wp--preset--color--base-2);
      @media (min-width: $break-large) {
        grid-template-columns: 4fr 4rem 6fr;
        grid-template-rows: auto auto;
        padding: 0;
      }
    }

    .contact-us-back-to-locations {
      display: block;
      margin-top: $theme-margin;
      a:not(.btn-primary) {
        background-color: var(--wp--preset--color--brand-color);
        color: #f2f2f2;
        padding: 7px 15px;
        text-decoration: none;
      }
      @media (min-width: $break-large) {
        display: none;
      }
    }

    .location-school-info {
      grid-area: 1 / 1 / 2 / 2;
      @media (min-width: $break-large) {
        grid-area: 1 / 1 / 2 / 2;
        padding: 4rem 0 0 4rem;
      }
      .location-contact-us-school-name {
        color: var(--wp--preset--color--brand-color);
        padding-bottom: 10px;
      }
      .location-contact-us-school-info {
        padding-bottom: 10px;
        font-size: 0.9rem;
      }
      .location-contact-us-address,
      .location-contact-us-phone-number,
      .location-contact-us-email {
        display: flex;
        margin-bottom: 0.3rem;
        i.fas {
          width: 1.2rem;
          color: var(--wp--preset--color--brand-color);
        }
      }
      .location-school-hours {
        padding-bottom: 10px;
        h1,
        h2,
        h3,
        h4,
        h5 {
          color: var(--wp--preset--color--brand-color);
        }
        p {
          font-size: 0.9rem;
        }
      }
    }

    .location-lead-capture-form {
      grid-area: 3 / 1 / 4 / 2;
      @media (min-width: $break-large) {
        grid-area: 2 / 1 / 3 / 2;
        padding: 0 0 4rem 4rem;
      }
      iframe {
        width: 100%;
        height: var(--contact-us-form-container-height);
      }
      div {
        width: 100%;
        padding: 2rem 1rem 0;
        text-align: center;
        border: 2px solid var(--wp--preset--color--card-color);
        background: repeating-linear-gradient(
          135deg,
          var(--wp--preset--color--base),
          var(--wp--preset--color--base) 10px,
          var(--wp--preset--color--base-2) 10px,
          var(--wp--preset--color--base-2) 20px
        );
        color: var(--wp--preset--color--primary-text);
        font-weight: bold;
        height: var(--contact-us-form-container-height);
      }
    }

    .location-google-map-container {
      grid-area: 2 / 1 / 3 / 2;
      @media (min-width: $break-large) {
        height: auto;
        grid-area: 1 / 3 / 3 / 4;
        margin-bottom: none;
      }
    }

    .location-google-map {
      height: 100%;
      overflow: hidden;
      background-color: transparent;
      border: none;
      border-radius: 0;
      .accordion-collapse {
        margin-bottom: 20px;
        @media (min-width: $break-large) {
          height: 100%;
        }
      }
      .accordion-header {
        margin-bottom: 20px;
        @media (min-width: $break-large) {
          display: none;
          margin-bottom: none;
        }
        .accordion-button {
          background-color: var(--wp--preset--color--brand-color);
          padding: 1rem;
          &::after {
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23f2f2f2'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
          }
        }
      }
      .acf-map {
        height: 450px;
        @media (min-width: $break-large) {
          height: 100%;
        }
        h3,
        p {
          color: #1b1b1b;
          padding: 0.25rem 1rem 0.25rem 0.75rem;
        }
        h3 {
          font-size: 1.1rem;
        }
        p {
          font-size: 0.9rem;
          a {
            padding: 5px 10px;
            font-size: 0.8rem;
          }
        }
      }
    }
  }

  .contact-us-heading {
    h1 {
      &.has-font-size {
        font-size: var(--contact-us-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--contact-us-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--contact-us-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--contact-us-heading-custom-word-spacing);
      }
    }
  }
  .location-school-info {
    h3 {
      &.has-font-size {
        font-size: var(--contact-us-school-name-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--contact-us-school-name-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--contact-us-school-name-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--contact-us-school-name-custom-word-spacing);
      }
    }
  }
}
