section.webform {
  // Block Gradient Styles
  @include block-gradient-border(
    var(--webform-block-gradient-border),
    var(--webform-padding-top),
    var(--webform-padding-bottom)
  );

  .webform-placeholder-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
    background-color: #ededed;
  }

  &.flip-column-content {
    .row {
      flex-direction: row-reverse;
    }
  }
}

.container.webform-cpt {
  $form-padding: 0 40px; // padding to center content with iframe lead capture form

  width: 100%;
  max-width: 100%;

  .webform-content-container,
  .webform-image-container {
    padding: 0;
  }

  .webform-content-container {
    position: relative;
    .background-texture {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      z-index: 0;
    }
  }

  .webform-location-info {
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    z-index: 1;

    .webform-location-forms {
      position: relative;
      overflow: hidden;
      .webform-loading-screen {
        display: flex;
        justify-content: center;
        align-items: center;
        &.disabled {
          visibility: hidden;
          position: absolute;
        }
      }
      .location-information {
        position: relative;
        visibility: visible;
        &.inactive-school {
          visibility: hidden;
          position: absolute;
          top: 0;
        }
      }
      .website-form-container {
        .location-iframe {
          height: var(--webform-form-container-height);
        }
      }
    }

    .webform-selection-dropdown {
      display: flex;
      flex-direction: column;
      padding: $form-padding;
      margin-bottom: 1rem;
      #school-location {
        max-width: 100%;
        color: inherit;
        background-color: inherit;
        padding: 5px;
        border: 2px solid;
        border-radius: 0;
      }
    }
  }

  .location-info {
    display: flex;
    .location-icon {
      min-width: 1rem;
      margin-right: 5px;
    }
  }

  .page-section-header,
  .location-info.location-number,
  .location-info.location-email,
  .location-info.location-address {
    padding: $form-padding;
  }

  .location-info.location-address {
    margin-bottom: 1rem;
  }

  .location-info.location-iframe {
    iframe {
      width: 100%;
      height: var(--webform-form-container-height);
      border: none;
    }
    div {
      width: 100%;
      padding: 2rem 1rem 0;
      margin: 0 40px;
      text-align: center;
      border: 2px solid var(--wp--preset--color--card-color);
      background: repeating-linear-gradient(
        135deg,
        var(--wp--preset--color--base),
        var(--wp--preset--color--base) 10px,
        var(--wp--preset--color--base-2) 10px,
        var(--wp--preset--color--base-2) 20px
      );
      color: var(--wp--preset--color--primary-text);
      font-weight: bold;
      height: var(--webform-form-container-height);
    }
  }

  .webform-image {
    height: 100%;
    position: relative;
    overflow: hidden;
    img {
      position: relative;
      width: 100%;
      height: 100%;
      @media (min-width: $break-large) {
        position: absolute;
      }
    }
    &.has-image-background-color {
      background-color: var(--webform-image-background-color);
    }
  }
}
