.blog,
.archive,
.search,
.single {
  padding-bottom: $half-padding;
  .wp-block-post-date {
    color: var(--wp--preset--color--primary-text);
  }
}
/*
 * Search Bar styles 
 */
.wp-block-search__inside-wrapper {
  height: 50px;

  .wp-block-search__input {
    font-size: 1.1rem;
    border-radius: 0;
    background-color: var(--wp--preset--color--base);
    color: var(--wp--preset--color--primary-text);
    border: 1px solid var(--wp--preset--color--primary-text);
  }

  #searchsubmit {
    border-radius: 0;
    padding: 0 2rem;
    @media (max-width: $break-large) {
      padding: 0 1rem;
      &::after {
        font: var(--fa-font-light);
        content: "\f002";
        font-size: 1.4rem;
      }
    }
    .bi.bi-search {
      display: none;
    }
  }

  ::placeholder {
    opacity: 0.75; /* Firefox */
  }
}

/*
 * Styles for the "viewing: (category)" section
 */

.page-section-blog {
  padding: 0 $theme-padding;
  @media (min-width: $break-large) {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0;
  }
}

#blog-search-query-container {
  @media (min-width: $break-medium) {
    padding: 0;
  }
  .search_query {
    display: flex;
    margin: 0 0 $half-margin 0;
  }
  h5 {
    margin: 0;
    color: var(--blog-post-section-single-post-color);
  }
  .blog-search-query-button {
    display: flex;
    margin-left: $half-margin;
    background-color: var(--custom-acf-primary-brand-color);
    color: #f2f2f2;
    padding: 2px 10px;
    text-decoration: none;
    &:hover {
      filter: brightness(1.1);
    }
  }
  .search-query-category {
    text-transform: uppercase;
  }
  .bi.bi-x-lg {
    margin-left: $half-margin;
    -webkit-text-stroke: 1px;
  }
}

.post-container {
  padding: 0;
}

#recent-blog-post-list {
  @media (min-width: $break-medium) {
    padding: 0 $half-padding 0 0;
  }
}

.blog-post-container {
  .side-bar-quick-links {
    display: flex;
    justify-content: space-between;
    @media (min-width: $break-medium) {
      display: none;
    }
  }
}

.blog-post-content {
  padding: 1.5rem 2rem 2rem;
  background-color: var(--blog-post-section-single-post-background);
  color: var(--blog-post-section-single-post-color);
  @media (min-width: 576px) {
    padding: 2rem 3.5rem 3.5rem;
  }
}

.previous_next_container {
  display: flex;
  justify-content: space-between;
}

.blog-title {
  padding-bottom: $half-padding;
  border-bottom: 1px solid var(--custom-acf-primary-brand-color);
  @media (min-width: $break-large) {
    padding-bottom: 0;
    border-bottom: none;
  }
  h3 {
    font-size: 1.5rem;
    margin: 0;
    @media (min-width: $break-medium) {
      font-size: 2rem;
    }
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.blog-published {
  margin-top: $half-margin;
}

.blog_excerpt {
  padding-bottom: $theme-padding;
  a {
    width: 100%;
  }
  p {
    margin: $half-margin 0;
  }
}

/*
 * Sidebar Styles - Recent Posts & Categories Accordions
 */

#recent-posts-and-categories {
  h2 {
    width: 100%;
    span {
      color: var(--wp--preset--color--primary-text);
      font-size: 1.5rem;
      text-transform: uppercase;
    }
  }

  .accordion-button {
    background-color: transparent;
    padding: 0 0 $half-padding 0;
    border-bottom: none;
    box-shadow: none;
    &.collapsed {
      border-bottom: 1px solid var(--wp--preset--color--primary-text);
    }
    &::after {
      font: var(--fa-font-light);
      content: "\f077";
      background-image: none;
      color: var(--wp--preset--color--primary-text);
    }
    &:focus {
      border-color: var(--wp--preset--color--primary-text);
    }
  }

  .accordion-collapse {
    background-color: var(--wp--preset--color--base-2);
  }

  .accordion-body {
    padding: 0;
    li {
      margin-bottom: 1rem;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    a {
      color: var(--wp--preset--color--brand-color);
      text-decoration: none;
      &:hover {
        color: var(--wp--preset--color--primary-text);
        text-decoration: underline;
      }
    }
  }

  .wp-block-categories {
    padding-top: var(--wp--preset--spacing--10);
    padding-bottom: var(--wp--preset--spacing--10);
    padding-left: var(--wp--preset--spacing--10);
    padding-right: var(--wp--preset--spacing--10);
  }
}

.pagination {
  padding: 20px;
  font-size: 1.5rem;
  justify-content: center;
  .page-numbers {
    padding: 0 5px;
  }
}
.blog_info {
  p {
    margin-bottom: 0.25rem;
  }
}

/*
 * Styles - Posts list and Single Post 
*/

#back-to-blog-page .bi.bi-chevron-left {
  margin-right: 0.5rem;
}

.blog-single-post-container {
  padding-top: $theme-padding;
}

.wp-block-post {
  margin: $theme-margin 0;
  @media (min-width: $break-medium) {
    margin: 0 0 $theme-margin;
  }
}

.blog-featured-images {
  .wp-block-post-featured-image {
    height: 250px;
    @media (min-width: $break-large) {
      height: 100%;
    }
  }
  a {
    display: block;
    height: 100%;
    position: relative;
    @media (min-width: $break-large) {
      height: 100%;
    }
  }
  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
  }
}

.page-section-single-post {
  padding: 0;
  @media (min-width: $break-large) {
    margin: 0 auto;
  }
  #recent-posts-and-categories {
    padding-top: $theme-padding;
  }

  .wp-block-post-featured-image {
    img {
      max-height: 400px;
    }
  }
}

.single-post {
  header {
    position: relative;
    z-index: 2;
  }

  .wp-block-cover {
    justify-content: start;
    align-items: end;
    padding-bottom: 1.75rem;
    &.has-background-position {
      img {
        object-position: var(--single-blog-post-featured-image-position);
      }
    }
    &.has-cover-height {
      min-height: var(--single-post-cover-image-block-height);
    }
  }
  .wp-block-cover__inner-container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    #back-to-blog-page {
      margin-bottom: 0.75rem;
    }
    hr {
      opacity: 1;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      @media (min-width: $break-large) {
        margin-right: 2.25%;
      }
    }
  }

  .blog-share-to-socials {
    @media (max-width: 781px) {
      h4 {
        text-align: center;
      }
      .blog-social-media {
        justify-content: center;
        margin-top: 1rem;
      }
    }
  }

  .blog-sidebar-expand {
    position: fixed;
    right: 0;
    top: 50%;
    z-index: 3;
    .wp-element-button {
      padding: 1rem 1.25rem;
    }
  }

  .modal-backdrop {
    opacity: 0.6;
    z-index: 2;
    position: fixed;
  }
}

.single-post-content {
  background-color: var(--wp--preset--color--base-2);
  h2,
  h3,
  h4,
  h5 {
    color: var(--wp--preset--color--brand-color);
    text-transform: uppercase;
  }
}
.single-post-info {
  padding-bottom: $half-padding;
  h1 {
    border-bottom: 1px solid var(--wp--preset--color--brand-color);
    padding-bottom: 1rem;
  }
  a {
    color: var(--wp--preset--color--brand-color);
  }
}

.blog-social-media {
  display: flex;
  margin-top: 1rem;
  .facebook-share-btn {
    margin-right: 0.5rem;
  }
}

.previous_next_container {
  display: flex;
  justify-content: space-between;
  height: 40px;
  margin-bottom: $theme-margin;
  @media (min-width: $break-large) {
    margin-bottom: $half-margin;
  }
  .bi.bi-chevron-left {
    margin-right: 0.5rem;
  }
  .bi.bi-chevron-right {
    margin-left: 0.5rem;
  }
}

.search_query {
  margin: 20px 0;
  h3 {
    text-align: center;
  }
}
