#footer {
  text-align: center;
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--site-footer-block-gradient-border),
    var(--site-footer-padding-top),
    var(--site-footer-padding-bottom)
  );

  .background-texture {
    background-image: var(--footer-background-texture);
    background-size: cover;
    background-position: var(--footer-background-texture-position);
    opacity: var(--footer-background-texture-opacity);
    background-repeat: var(--footer-background-texture-repeat);
    transform: var(--footer-background-texture-transform);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    position: absolute;
    @media (min-width: $break-large) {
      background-size: var(--footer-background-texture-size);
    }
  }

  .footer-container {
    position: relative;
    .menu-item {
      font-size: 1.125rem;
    }

    &.has-link-text-color {
      a {
        color: var(--header-nav-link-text-color);
      }
    }
    &.has-link-text-hover-color {
      a {
        &:hover {
          color: var(--header-nav-link-text-hover-color);
        }
      }
    }

    .footer-menus {
      h6 {
        font-size: 1.3rem;
      }
      &.has-font-size {
        h6 {
          font-size: var(--site-footer-heading-custom-font-size);
        }
      }
      &.has-font-weight {
        h6 {
          font-weight: var(--site-footer-heading-custom-font-weight);
        }
      }
      &.has-line-height {
        h6 {
          line-height: var(--site-footer-heading-custom-line-height);
        }
      }
      &.has-letter-spacing {
        h6 {
          letter-spacing: var(--site-footer-heading-custom-letter-spacing);
        }
      }
      &.has-heading-color {
        h6 {
          color: var(--site-footer-custom-heading-color);
        }
      }
    }
  }

  a {
    text-decoration: none;
    span {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  h6 {
    color: var(--wp--preset--color--brand-color);
    padding: 0.25rem 0.5rem;
  }

  #menu-footer {
    margin-left: 0.5rem;
  }

  p {
    font-size: 0.9rem;
  }

  .footer-logo {
    display: flex;
    flex-direction: column;
    .school-logo {
      display: flex;
      justify-content: center;
      margin-bottom: 2rem;
      img {
        width: calc(var(--site-footer-site-logo-size) * 0.8);
        @media (min-width: $break-large) {
          width: var(--site-footer-site-logo-size);
        }
      }
    }
    .footer-image {
      display: flex;
      justify-content: center;
      margin-bottom: 1rem;
      img {
        width: calc(var(--site-footer-image-size) * 0.8);
        @media (min-width: $break-large) {
          width: var(--site-footer-image-size);
        }
      }
    }
  }

  .col-lg {
    margin-bottom: 1rem;
  }

  .col-lg-2,
  .col-lg-3 {
    margin-bottom: 2rem;
    @media (min-width: $break-large) {
      margin-bottom: 0;
    }
  }

  .footer-brand {
    display: inline-block;
    margin-bottom: 2rem;
  }

  .footer-location {
    margin-bottom: 1rem;
    margin-left: 0.5rem;
    @media (min-width: $break-large) {
      margin-bottom: 0;
    }
    a {
      display: flex;
      justify-content: center;
      @media (min-width: $break-large) {
        justify-content: flex-start;
      }
      i {
        margin-top: 4px;
        margin-right: 5px;
        min-width: 15px;
      }
    }
    p {
      margin: 0 0 0.2rem 0;
    }
    .footer-location-label {
      font-size: 1.125rem;
    }
    .footer-phone,
    .footer-address,
    .footer-email {
      margin-left: 0.5rem;
    }
    .footer-school-social {
      display: flex;
      font-size: 2rem;
      margin-left: 0.5rem;
      justify-content: center;
      @media (min-width: $break-large) {
        justify-content: flex-start;
      }
      .social-account {
        margin: 0 0.7rem 0 0;
        display: flex;
        justify-content: center;
        text-decoration: none;
        i:before {
          font-size: 2rem;
          @media (min-width: $break-large) {
            font-size: 1.5rem;
          }
        }
        img {
          object-fit: contain;
          &:hover {
            filter: brightness(0.8);
          }
        }
      }
    }
  }

  .footer-kicksite-branding {
    justify-content: center;
    margin-top: 2rem;
    .copyright {
      text-align: center;
    }
  }
}

.business-hours {
  margin-left: 0.5rem;
  p {
    margin-top: 0;
  }
}

@media (min-width: $break-large) {
  #footer {
    text-align: left;
    .col-sm {
      margin-bottom: 0;
    }
    .footer-location {
      margin-bottom: 0.75rem;
    }
  }
}
