section.billboard-multi-location {
  width: 100%;
  background-position: center;
  position: relative;
  overflow: hidden;
  padding: 0;
  background-size: cover;
  background-repeat: no-repeat;
  @media (min-width: $break-small) {
    min-height: 70vh;
  }

  // Block Gradient Styles
  @include block-gradient-border(
    var(--billboard-multi-location-block-gradient-border),
    var(--billboard-multi-location-padding-top),
    var(--billboard-multi-location-padding-bottom)
  );

  // Styles specific to positioning manually uploaded mp4 videos
  &.has-background-video {
    #background-video {
      min-width: 100%;
      min-height: 100%;
      position: absolute;
      top: 0;
    }
    video {
      max-width: 100%;
      object-fit: cover;
      object-position: center;
    }
    .video-controls {
      display: flex;
      justify-content: center;
      z-index: 1;
      justify-self: center;
      margin: 1rem 0;
      position: relative;
      button#toggle {
        height: 50px;
        width: 130px;
        font-style: italic;
        border: none;
        border-radius: $border-radius;
        color: var(--dark-text);
        &.playing {
          background-color: rgba(255, 255, 255, 0.6);
        }
        &.paused {
          background-color: rgba(255, 255, 255, 0.9);
        }
        @media (min-width: $break-medium) {
          height: 30px;
          width: 130px;
        }
      }
    }
  }

  // Styles specific to embed videos added through the "YouTube Video ID" field
  &.has-background-embed {
    grid-template-rows: 10px 1fr auto auto 20px;
    @media (min-width: $break-large) {
      grid-template-rows: 20px 1fr auto 1fr 30px;
    }
    .multi-location-billboard-video.embed {
      position: absolute;
      overflow: hidden;
      width: 100vw;
      height: 100vh;
      z-index: 0;
      background-color: var(--image-background-color);
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: var(--brightnessOverlay);
        z-index: 1;
      }
      iframe {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100vw;
        height: 100vh;
        transform: translate(-50%, -50%);
        @media (min-aspect-ratio: 16/9) {
          height: 56.25vw;
        }
        @media (max-aspect-ratio: 16/9) {
          width: 177.78vh;
        }
      }
    }
  }

  .multi-location-background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    picture {
      width: 100%;
      height: 100%;
      position: absolute;
    }
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background-color: var(
        --brightnessOverlay
      ); // adds color and opacity choice set in the page section design settings
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: var(--mobile-background-image-size);
      object-position: var(--mobile-background-image-position);
      @media (min-width: $break-medium) {
        object-fit: var(--desktop-background-image-size);
        object-position: var(--desktop-background-image-position);
      }
    }
    .multi-location-billboard-background-texture {
      width: 100%;
      height: 100%;
      position: relative;
    }
  }

  .billboard-video.embed {
    width: 100vw;
    height: 100%;
    position: absolute;
    overflow: hidden;
    iframe {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .multi-location-content {
    padding: 10vw 5vw;
    min-height: 675px;
    justify-content: center;
    align-content: center;
    position: relative;
    @media (min-width: $break-medium) {
      min-height: 100vh;
    }
    @media (min-width: $break-xLarge) {
      padding: 10vh 10vw;
    }
    @media (min-width: $break-xxl) {
      padding: 10vh 0;
      max-width: 1200px;
      margin: 0 auto;
    }
    .location-column {
      margin-bottom: $theme-margin;
      &.has-card-styles {
        .multi-layout-billboard-locations {
          background-color: var(
            --multi-location-billboard-card-background-color
          );
        }
        .multi-location-heading-container {
          h4 {
            color: var(--multi-location-billboard-card-header-color);
          }
          h5 {
            color: var(--multi-location-billboard-card-address-color);
          }
        }
      }
    }
    .multi-layout-billboard-locations {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;
      background-color: #ededed;
      @media (min-width: $break-small) {
        grid-template-columns: 100px 100px 6fr;
      }
      &.has-location-image {
        .multi-location-address {
          .multi-location-heading-container {
            @media (min-width: $break-small) {
              padding: 0 $half-padding;
            }
          }
        }
      }
    }
  }

  .multi-location-billboard-header {
    display: flex;
    flex-direction: column;
    justify-content: end;
    margin-bottom: $theme-margin;
    padding: 0 $theme-padding;
    @media (min-width: $break-small) {
      margin: 0 auto;
      padding: 0 5vw;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    p {
      color: var(
        --multi-location-section-color
      ); // Adds text color set in the page section design settings
      text-align: center;
    }
    h1 {
      font-size: 2rem;
      margin: 0.25rem auto 1rem;
      @media (min-width: $break-small) {
        font-size: 4rem;
        margin: 10px 0 20px;
      }
    }
    h2 {
      font-size: 1.5rem;
      @media (min-width: $break-small) {
        font-size: 2.5rem;
      }
    }
    h3 {
      font-size: 1.125rem;
      @media (min-width: $break-small) {
        font-size: 1.75rem;
        margin-bottom: 40px;
      }
    }
  }

  .multi-location-img {
    display: none;
    @media (min-width: $break-small) {
      display: block;
      // margin: $theme-margin 0;
      padding: 0;
      grid-area: 1 / 1 / 2 / 3;
      align-self: center;
      width: 100%;
      height: 100%;
    }
    img {
      position: relative;
      object-fit: cover;
      filter: brightness(0.75);
      object-position: var(
        --locationImgPosition
      ); // sets the location image position from page section design settings
      @media (min-width: $break-small) {
        width: 100%;
        height: 100%;
        margin-right: 0;
      }
    }
  }

  .multi-location-address {
    width: 100%;
    grid-column: 1/2;
    grid-row: 1/2;
    place-self: center;
    margin: 10px 0 20px;
    @media (max-width: $break-small) {
      display: grid;
      grid-template-columns: 4fr 1fr;
      grid-template-rows: 1fr;
    }
    @media (min-width: $break-small) {
      grid-column: 3/5;
      grid-row: 1/2;
    }
    .multi-location-heading-container {
      display: flex;
      align-items: center;
      padding-left: $half-padding;
      grid-area: 1 / 1 / 2 / 2;
      border-left: 1px solid gray;
      border-bottom: 1px solid gray;
      @media (min-width: $break-small) {
        flex-direction: column;
        padding: $theme-padding;
        max-width: 500px;
        border: none;
      }
    }
    h4 {
      width: 90%;
      padding-top: 10px;
      font-size: 1rem;
      font-weight: 600;
      text-transform: uppercase;
      color: var(--custom-acf-primary-brand-color);
      @media (min-width: $break-small) {
        width: 100%;
      }
    }
    h5 {
      display: none;
      font-weight: 200;
      @media (min-width: $break-small) {
        display: block;
        padding-bottom: 10px;
        font-size: 0.8rem;
        color: var(--multi-location-section-color);
      }
    }
    .multi-location-button-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 1rem;
      grid-area: 1 / 2 / 2 / 3;
      @media (min-width: $break-small) {
        max-width: 300px;
      }
    }
    .theme-right-arrow {
      stroke: #f2f2f2 !important;
    }
    .button-text {
      display: none;
    }
    .button-right-arrows {
      display: block;
      width: 25px;
    }
    @media (min-width: $break-small) {
      .button-text {
        display: block;
      }
      .button-right-arrows {
        display: none;
      }
    }
  }

  .multi-location-video-overlay {
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background-color: var(
        --brightnessOverlay
      ); // adds color and opacity choice set in the page section design settings
    }
  }
}
