section.billboard {
  // Block Gradient Styles
  @include block-gradient-border(
    var(--billboard-block-gradient-border),
    var(--billboard-padding-top),
    var(--billboard-padding-bottom)
  );
}

.billboard-container {
  position: relative;
  height: 700px;
  overflow: hidden;
  background-size: cover;
  background-position: center;

  @media (min-width: $break-small) {
    height: 90vh;
  }

  &.has-background-video {
    .billboard-video.embed {
      position: absolute;
      overflow: hidden;
      width: 100vw;
      height: 100vh;
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: var(--billboard-background-overlay-color);
        z-index: 1;
      }
      iframe {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100vw;
        height: 100vh;
        transform: translate(-50%, -50%);
        @media (min-aspect-ratio: 16/9) {
          height: 56.25vw;
        }
        @media (max-aspect-ratio: 16/9) {
          width: 177.78vh;
        }
      }
    }

    video {
      max-width: 100%;
      object-fit: cover;
      object-position: center;
    }
    .billboard-video-container {
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: var(--billboard-background-overlay-color);
        z-index: 1;
      }
    }
    #background-video {
      position: absolute;
      min-width: 100%;
      min-height: 100%;
    }

    .video-controls {
      width: 100%;
      position: absolute;
      bottom: 0;
      display: flex;
      justify-content: center;
      padding: 1rem 0;
      button {
        width: 120px;
        height: 35px;
        border: none;
        margin: auto;
        cursor: pointer;
        font-size: 0.7rem;
        &.playing {
          background-color: rgba(255, 255, 255, 0.6);
        }
        &.paused {
          background-color: rgba(255, 255, 255, 0.9);
        }
      }
    }

    .billboard-content {
      position: relative;
      z-index: 2;
    }
    .video-controls {
      z-index: 2;
    }
  }
  &.has-billboard-lcf {
    height: 100%;
    .billboard-content {
      position: relative;
      .container {
        margin: 0 auto;
      }
    }
    .billboard-lcf-container {
      h4 {
        margin-top: 0;
        padding: 1rem 0;
        font-size: 1.3rem;
        font-weight: 600;
      }
      iframe {
        width: 100%;
      }
    }
  }
  .billboard-background-image-container {
    width: 100%;
    height: 100%;
    .billboard-background-image {
      position: absolute;
      width: 100%;
      height: 100%;
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: var(--billboard-background-overlay-color);
      }
    }
    .background-texture {
      width: 100%;
      height: 100%;
      position: relative;
      top: 0;
    }
  }

  .billboard-content {
    width: 100%;
    height: 100%;
    padding: 2rem 0;
    position: absolute;
    top: 0;
    @media (min-width: $break-small) {
      padding: 5rem 3rem;
    }
    @media (min-width: $break-large) {
      padding: 5rem;
    }
    .container {
      margin: 0;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
      color: var(--billboard-content-text-color);
    }

    h1 {
      margin: 1.75rem 0;
      @media (min-width: $break-small) {
        margin: 1.5rem 0;
      }
    }
    h2 {
      margin: 1.5rem 0;
      @media (min-width: $break-small) {
        margin: 1.3rem 0;
      }
    }
    h3 {
      margin: 1.35rem 0;
      @media (min-width: $break-small) {
        margin: 1.2rem 0;
      }
    }
    h4 {
      margin: 1.3rem 0;
      @media (min-width: $break-small) {
        margin: 1.2rem 0;
      }
    }
    h5,
    h6 {
      margin: 1rem 0;
    }

    .billboard-header-content {
      padding: 0 1rem;
      @media (min-width: $break-large) {
        padding: 0;
      }
      &.has-heading-text-shadow {
        h1,
        h2,
        h3,
        h4 {
          text-shadow: var(--billboard-heading-text-shadow);
        }
      }
    }
    .billboard-link {
      margin: 2rem 1rem;
      @media (min-width: $break-large) {
        margin: 2rem 0;
      }
    }
  }
}
