section.text-block {
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--text-block-block-gradient-border),
    var(--text-block-padding-top),
    var(--text-block-padding-bottom)
  );

  .background-texture {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
  }

  &.thank-you-modal {
    height: 100vh;
    .text-block-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }

  .text-block-container {
    position: relative;
  }

  // Block Border Options Mixin found "/src/scss/mixins/gradient-border.scss"
  @include block-gradient-border(
    var(--text-block-block-gradient-border),
    var(--text-block-padding-top),
    var(--text-block-padding-bottom)
  );
}
