section.page-header {
  // Main Styles

  // Block Gradient Styles
  @include block-gradient-border(
    var(--page-header-block-gradient-border),
    var(--page-header-padding-top),
    var(--page-header-padding-bottom)
  );

  .container-fluid {
    padding: 0;
    .row {
      &.flip-column-content {
        flex-direction: row-reverse;
      }
    }
  }

  .page-header-container {
    position: relative;
    height: 20rem;
    &.single-column {
      height: 24rem;
    }
    @media (min-width: $break-large) {
      height: auto;
    }
    .page-header-image {
      width: 100%;
      height: 100%;
      position: absolute;
    }
  }

  .content-block-col {
    position: relative;
    .background-texture {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
    }
  }

  .page-header-content {
    position: relative;
    @media (min-width: $break-xlarge) {
      max-width: 700px;
      margin: 0 auto;
      .btn {
        margin-top: 1rem;
      }
    }
    .page-header-heading {
      font-size: max(2.5rem, 40px);
      &.has-font-size {
        font-size: var(--page-header-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--page-header-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--page-header-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--page-header-heading-custom-word-spacing);
      }
    }
  }

  .page-header-title {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 200;
    width: 100%;
    color: var(--custom-acf-primary-brand-color);
    h1 {
      display: inline-block;
      margin: 0;
      padding: 1rem;
    }
    .page-header-heading {
      font-size: max(2.5rem, 40px);
      &.has-font-size {
        font-size: var(--page-header-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--page-header-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--page-header-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--page-header-heading-custom-word-spacing);
      }
    }
  }

  .page-header-overlay {
    position: absolute;
    background: rgba(0, 0, 0, 0.2);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
