section.video-gallery {
  width: 100%;
  padding: 0;
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--video-gallery-block-gradient-border),
    var(--video-gallery-padding-top),
    var(--video-gallery-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .video-gallery-container {
    position: relative;
  }

  .heading-container {
    margin-bottom: 1rem;
    @media (min-width: $break-small) {
      margin-bottom: 2rem;
    }
  }
}

.card-gradient-border {
  .video-container-wrapper {
    background-image: var(--video-gallery-card-gradient-border);
  }
}

.video-container-wrapper {
  max-width: 100%;
  margin: 1rem;
  position: relative;
  overflow: hidden;
  @media (min-width: $break-small) {
    margin: 1rem 2rem;
  }
  @media (min-width: $break-large) {
    max-width: 1400px;
    margin: 0 auto;
  }
  &.has-gradient-card-border {
    .video-container {
      margin: var(--video-gallery-card-gradient-border-size);
    }
  }

  .video-container {
    display: flex;
    position: relative;
    flex-direction: column;
    background-color: var(--video-gallery-card-background-color);
    padding: 1rem;
    @media (min-width: $break-small) {
      padding: 2rem;
    }
    @media (min-width: $break-large) {
      flex-direction: row;
    }

    .card-background-texture {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }

    .video-player-container {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 400px;
      overflow: hidden;
      z-index: 1;
      @media (min-width: $break-small) {
        height: 450px;
      }
      @media (min-width: $break-large) {
        margin: 20px 0 20px 20px;
        width: 80%;
        height: 100%;
      }
      .video-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
        padding-top: 0;
        background-color: #1b1b1b;
        border: 2px solid #1b1b1b;
        overflow: hidden;
      }
      video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  .video-gallery-thumbnails {
    margin-top: 1rem;
    width: auto;
    z-index: 1;
    @media (min-width: $break-large) {
      margin: 20px 20px 20px 0;
      width: 20%;
      display: flex;
      flex-direction: column;
      justify-content: end;
    }
    .thumbnail-gallery-title {
      font-style: italic;
      margin: 0.5rem 0 0 0;
      @media (min-width: $break-large) {
        margin: 0 0 0 20px;
      }
      p {
        font-size: 1rem;
        margin-bottom: 0.5rem;
      }
    }
    .thumbnail-gallery-wrapper {
      display: flex;
      flex-direction: column;
      padding: 1rem;
      overflow-x: scroll;
      overflow-y: hidden;
      background-color: var(--thumbnail-gallery-background-color);
      margin: 0.5rem 0 0 0;
      border: 2px solid var(--thumbnail-gallery-border-color);
      @media (min-width: $break-small) {
        flex-direction: row;
        flex-wrap: wrap;
      }
      @media (min-width: $break-large) {
        flex-direction: column;
        height: var(--video-gallery-thumbnail-slider-height);
        padding: 1rem 1rem 0;
        overflow-x: hidden;
        overflow-y: scroll;
        margin: 0 0 0 20px;
        flex-wrap: nowrap;
      }
      .video-thumbnail {
        cursor: pointer;
        margin: 0 0 1rem;
        width: 100%;
        height: 100%;
        @media (min-width: $break-small) {
          width: 50%;
          &:nth-child(even) {
            padding-left: 0.5rem;
          }
          &:nth-child(odd) {
            padding-right: 0.5rem;
          }
        }
        @media (min-width: $break-large) {
          width: 100%;
          margin: 0 0 1rem;
          &:nth-child(even) {
            padding-left: 0;
          }
          &:nth-child(odd) {
            padding-right: 0;
          }
        }
        video {
          width: 100%;
          max-height: 200px;
          background-color: #1b1b1b;
          @media (min-width: $break-small) {
            height: 200px;
          }
          @media (min-width: $break-large) {
            width: 100%;
            max-height: 200px;
            height: auto;
          }
        }
        .thumbnail-video-details p {
          color: #1b1b1b;
          font-size: 0.8rem;
          margin: 0;
        }
      }
    }
  }
}
