/* Styling for Error 404 page Page */

.error-body {
  padding: 0 $theme-padding;
  @media (min-width: $break-large) {
    padding: 0;
  }
}

.error-body-content {
  h1 {
    margin-bottom: $half-margin;
  }
  h2 {
    margin-bottom: $theme-margin;
  }
  .wp-block-buttons {
    margin-top: $theme-margin;
  }
}
