/**
 * *******************************************************************
 * Block solid Border - This mixin will add the necesary styles for 
 * a top, bottom, or top and bottom solid border.
 * *******************************************************************
 */
@mixin block-override-solid-border($border-top, $border-bottom) {
  &.has-solid-border-top {
    .cta-block {
      border-top: $border-top;
      border-bottom: 0;
      border-image-source: none;
    }
  }
  &.has-solid-border-bottom {
    .cta-block {
      border-top: 0;
      border-bottom: $border-bottom;
      border-image-source: none;
    }
  }
  &.has-solid-border-topAndBottom {
    .cta-block {
      border-top: $border-top;
      border-bottom: $border-bottom;
      border-image-source: none;
    }
  }
}
