.offers-iframe-container {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.offers-dropdown {
  .dropdown-default {
    display: none;
  }
  .dropdown-menu {
    width: 100%;
    cursor: pointer;
    .dropdown-item {
      white-space: normal;
      &:active {
        background-color: initial;
      }
    }
  }
  .offer-select {
    color: black;
    background-color: white;
    width: 275px;
    max-width: 100%;
    padding: 0.5rem 1rem;
    border: 1px solid gray;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    white-space: normal;
    border: 1px solid;
    @media (min-width: 526px) {
      width: 350px;
    }
  }
}

.modal-dialog.offers-modal-dialog {
  max-width: 1000px;
  width: 94%;
  margin: 12vh auto 0;
  z-index: 1001;
  @media (max-width: $break-small) {
    margin-top: 5vh;
    width: 95vw;
  }
  .modal-content.offers-modal-content {
    background-color: var(--wp--preset--color--base);
    color: var(--wp--preset--color--primary-text);
    border-radius: 0;
    border: none;
    .modal-header.offers-modal-header {
      padding: 0.5rem 1rem;
      border-bottom: none;
      background-color: var(--wp--preset--color--base-2);
      button.close {
        font-size: 2rem;
        opacity: 1;
        font-weight: 400;
        background-color: transparent;
        border: none;
        color: var(--wp--preset--color--primary-text);
        margin-bottom: 0.3rem;
        @media (min-width: $break-medium) {
          font-size: 2.2rem;
        }
        &:hover,
        &:focus {
          opacity: 0.6;
        }
      }
      h5 {
        font-size: 1.125rem;
        color: var(--wp--preset--color--primary-text);
        @media (min-width: $break-medium) {
          font-size: 1.25rem;
        }
      }
    }
    .modal-body.offers-modal-body {
      height: 80vh;
      padding: 0;
      @media (min-width: $break-large) {
        height: 70vh;
      }
      iframe {
        width: 100%;
        height: 100%;
        border: none;
      }
    }
  }
}

.modal-backdrop {
  position: absolute;
  backdrop-filter: blur(5px);
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.offers-iframe-container.offer-location-select {
  h4 {
    color: var(--wp--preset--color--primary-text);
    text-align: center;
    margin-top: 3rem;
  }
}
