section.page-section-content-cards {
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--content-cards-block-gradient-border),
    var(--content-cards-padding-top),
    var(--content-cards-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .container {
    position: relative;
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--content-cards-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--content-cards-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--content-cards-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--content-cards-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--content-cards-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--content-cards-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--content-cards-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--content-cards-subheading-custom-word-spacing);
      }
    }
  }
}

#cards {
  position: relative;
  .row-eq-height {
    min-height: 400px;
  }

  .card-container {
    background: var(--wp--preset--color--base-2);
    max-width: 1400px;
    margin: 2rem auto;

    .card-gradient-border {
      background-image: var(--content-cards-card-gradient-border);
      padding: var(--content-cards-card-gradient-border-size);
    }

    &:nth-child(even) {
      .row-eq-height {
        flex-direction: row-reverse;
      }
    }

    .card-image-container {
      .card-image {
        position: relative;
        min-height: 250px;
        img {
          width: 100%;
          height: 100%;
          position: absolute;
        }
      }
    }
    .card-content-container {
      position: relative;
      .card-background-texture {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 0;
      }
      .card-content {
        padding: 2rem;
        position: relative;
        .card-link {
          @media (max-width: $break-large) {
            display: flex;
            justify-content: center;
          }
        }
        @media (min-width: $break-medium) {
          padding: 4rem;
        }
      }
    }
  }
}
