.testimonial-card-footer {
  display: grid;
  grid-template-columns: 3fr 1fr;
}

.testimonial-card-footer img {
  border-radius: 50%;
  width: 75px;
  max-width: fit-content;
  height: 75px;
  object-fit: cover;
}

.testimonial-card-body {
  background-color: rgba(0, 0, 0, 0.03);
}

.card.testimonial-gallery-card {
  margin-bottom: 30px;
}

.testimonial-gallery-image {
  display: flex;
  align-items: center;
}

.page-section-testimonial-gallery {
  .card-columns {
    @media (min-width: $break-small) {
      column-count: 2;
    }
    @media (min-width: $break-large) {
      column-count: 3;
    }
  }
}
