/*
 * Sidebar Styles - Recent Posts & Categories Accordions
 */

#recent-posts-and-categories,
.recent-posts-and-categories {
  .sidebar-accordion-container {
    margin-bottom: 1rem;
  }
  h5 {
    width: 100%;
    span {
      color: var(--wp--preset--color--primary-text);
      font-size: 1.2rem;
      text-transform: uppercase;
    }
  }

  .accordion-button {
    background-color: transparent;
    padding: 0 0 $half-padding 0;
    border-bottom: none;
    box-shadow: none;
    &.collapsed {
      border-bottom: 1px solid var(--wp--preset--color--primary-text);
    }
    &::after {
      font: var(--fa-font-light);
      content: "\f077";
      background-image: none;
      color: var(--wp--preset--color--primary-text);
    }
    &:focus {
      border-color: var(--wp--preset--color--primary-text);
    }
  }

  .accordion-collapse {
    background-color: var(--wp--preset--color--base-2);
  }

  .accordion-body {
    padding: 0;
    li {
      margin-bottom: 1rem;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    a {
      color: var(--wp--preset--color--brand-color);
      text-decoration: none;
      &:hover {
        color: var(--wp--preset--color--primary-text);
        text-decoration: underline;
      }
    }
  }

  .wp-block-categories {
    padding-top: var(--wp--preset--spacing--10);
    padding-bottom: var(--wp--preset--spacing--10);
    padding-left: var(--wp--preset--spacing--10);
    padding-right: var(--wp--preset--spacing--10);
  }
}

.pagination {
  padding: 20px;
  font-size: 1.5rem;
  justify-content: center;
  .page-numbers {
    padding: 0 5px;
  }
}
.blog_info {
  p {
    margin-bottom: 0.25rem;
  }
}
