// Main Navigation
// --------------------------------------------------
.site-header {
  position: relative;
  .navbar {
    padding: 0;
  }

  .navbar-nav {
    .dropdown-toggle::after {
      display: none;
    }
  }

  // Block Gradient Styles
  @include block-gradient-border(
    var(--site-header-block-gradient-border),
    var(--site-header-padding-top),
    var(--site-header-padding-bottom)
  );

  .background-texture {
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    position: absolute;
  }

  .navigation-header {
    position: relative;
  }

  &.has-custom-drop-shadow {
    z-index: 1;
    box-shadow: var(--site-header-drop-shadow);
  }
}

.navbar {
  .school-logo {
    display: flex;
    align-items: center;
    text-decoration: none;

    .school-name {
      display: none;
      @media (min-width: $break-large) {
        display: block;
        color: var(--header-nav-school-name-color);
        margin: 0 0 0.25rem 1rem;
        &:hover {
          filter: brightness(1.2);
        }
      }
    }
    img {
      width: calc(var(--site-header-site-logo-size) * 0.8);
      @media (min-width: $break-large) {
        width: var(--site-header-site-logo-size);
      }
    }
  }

  .toggle-container {
    padding-right: 1rem;
    .navbar-toggler {
      border: 0;
      color: var(--header-nav-link-text-color);
      font-size: 1.4rem;
      .navbar-toggler-icon {
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
      }
    }
  }

  .navbar-collapse {
    justify-content: flex-end;
    .navbar-nav {
      padding: 1rem;
      #menu-header {
        padding-top: 2rem;
      }
      @media (min-width: $break-xlarge) {
        #menu-header {
          padding-top: 0;
        }
        padding: 0;
      }
      &.navbar-search {
        display: none;
        @media (min-width: $break-xlarge) {
          display: block;
          margin-right: 1rem;
          i {
            font-size: 0.8rem;
            &:hover {
              cursor: pointer;
            }
          }
        }
        &.has-custom-link-colors {
          i {
            color: var(--header-nav-link-text-color);
            &:hover {
              color: var(--header-nav-link-text-hover-color);
            }
          }
        }
      }
      &#menu-header {
        @media (min-width: $break-xlarge) {
          margin-right: 1rem;
        }
      }
      &.navbar-links {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        @media (min-width: $break-medium) {
          justify-content: space-evenly;
        }
        @media (min-width: $break-xlarge) {
          li {
            width: auto;
            &:first-of-type {
              padding-left: 0;
            }
            padding: 0.2rem 0.4rem 0.2rem 1rem;
          }
        }
        .btn-login,
        .btn-signup {
          padding: 0.75rem 1rem;
          @media (min-width: $break-medium) {
            padding: 1rem 1.5rem;
          }
        }
      }
    }
    .nav-item {
      .dropdown-menu {
        display: block;
        border-radius: 0;
        border-color: var(--wp--preset--color--primary-text);
        @media (min-width: $break-xlarge) {
          display: none;
        }
      }
      @media (min-width: $break-xlarge) {
        &:hover {
          .dropdown-menu {
            display: block;
          }
        }
      }
      .dropdown-item {
        font-size: 0.8rem;
      }
    }

    &.has-dropdown-menu-colors {
      .dropdown-menu {
        background-color: var(--header-submenu-background-color);
        a {
          color: var(--header-submenu-text-color);
          background-color: var(--header-submenu-background-color);
          transition: all 0.2s ease-in-out;
          @media (min-width: $break-xlarge) {
            &:hover {
              background-color: var(--header-submenu-hover-color);
            }
          }
        }

        &.has-font-size {
          a {
            font-size: var(--site-header-dropdown-link-custom-font-size);
          }
        }
        &.has-font-weight {
          a {
            font-weight: var(--site-header-dropdown-link-custom-font-weight);
          }
        }
        &.has-line-height {
          a {
            line-height: var(--site-header-dropdown-link-custom-line-height);
          }
        }
        &.has-letter-spacing {
          a {
            letter-spacing: var(
              --site-header-dropdown-link-custom-letter-spacing
            );
          }
        }
        &.has-word-spacing {
          a {
            word-spacing: var(--site-header-dropdown-link-custom-word-spacing);
          }
        }
        &.has-letter-case {
          a {
            text-transform: var(--site-header-dropdown-link-custom-letter-case);
          }
        }
      }
    }

    &.nav-links-as-links {
      .nav-link {
        font-size: 1.2rem;
        @media (min-width: $break-xlarge) {
          font-size: 1rem;
        }
        &:not(.btn-signup, .btn-login) {
          color: var(--header-nav-link-text-color);
          &:hover {
            color: var(--header-nav-link-text-hover-color);
          }
        }
      }
      &.has-custom-link-colors {
        #menu-header {
          .menu-item {
            a {
              color: var(--header-nav-link-text-color);
              &:hover {
                color: var(--header-nav-link-text-hover-color);
              }
            }
          }
        }
      }
    }

    &.nav-links-as-buttons {
      #menu-header {
        padding: 1rem 1rem 0;
        @media (min-width: $break-xlarge) {
          padding: 0;
        }
        .nav-item {
          display: flex;
          justify-content: center;
          width: 100%;
          padding: 1rem 0 0;
          @media (min-width: $break-xlarge) {
            display: block;
            width: auto;
            padding: 0.2rem 0.4rem 0.2rem 1rem;
          }
        }
        .nav-link {
          width: 100%;
          max-width: 400px;
          padding: 0.75rem 1rem;
          @media (min-width: $break-medium) {
            padding: 1rem 1.5rem;
          }
        }
      }

      .navbar-links {
        flex-direction: column;
        padding: 0 1rem 1rem;
        @media (min-width: $break-xlarge) {
          flex-direction: row;
          padding: 0;
        }
        li {
          display: flex;
          justify-content: center;
          width: 100%;
          padding: 1rem 0 0;
          @media (min-width: $break-xlarge) {
            display: block;
            width: auto;
            padding: 0.2rem 0.4rem 0.2rem 1rem;
          }
        }
        a {
          width: 100%;
          max-width: 400px;
          padding: 0.75rem 1rem;
          @media (min-width: $break-medium) {
            padding: 1rem 1.5rem;
          }
        }
      }
    }
  }

  &.bg-transparent-light {
    background: rgba(255, 255, 255, 0.5);
    @media (max-width: $break-small) {
      background: rgba(255, 255, 255, 0.3);
    }
    .navbar-nav {
      a {
        @media (min-width: $break-xlarge) {
          padding: 1rem;
        }
      }
    }
  }

  &.navbar-expand-xl {
    .navbar-nav {
      .nav-link {
        @media (min-width: $break-xlarge) {
          padding-left: 0.75rem;
          padding-right: 0.75rem;
          text-align: center;
        }
      }
    }
  }

  .navbar-nav {
    &.has-font-size {
      .nav-link {
        font-size: var(--site-header-link-custom-font-size);
      }
    }
    &.has-font-weight {
      .nav-link {
        font-weight: var(--site-header-link-custom-font-weight);
      }
    }
    &.has-line-height {
      .nav-link {
        line-height: var(--site-header-link-custom-line-height);
      }
    }
    &.has-letter-spacing {
      .nav-link {
        letter-spacing: var(--site-header-link-custom-letter-spacing);
      }
    }
    &.has-word-spacing {
      .nav-link {
        word-spacing: var(--site-header-link-custom-word-spacing);
      }
    }
    &.has-letter-case {
      .nav-link {
        text-transform: var(--site-header-link-custom-letter-case);
      }
    }
  }

  &.padding-fix {
    padding: 1rem 0 0;
  }
}

//
// Footer Navigation
// --------------------------------------------------

.nav-footer {
  li {
    margin-bottom: 1rem;
    @media (min-width: $break-small) {
      margin-bottom: 0.2rem;
    }
  }
}
