section.page-section-masonry-gallery {
  padding: 0;
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--masonry-gallery-block-gradient-border),
    var(--masonry-gallery-padding-top),
    var(--masonry-gallery-padding-bottom)
  );

  .background-texture {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
  }
  .masonry-gallery {
    position: relative;
  }

  .card-columns {
    @media (min-width: $break-medium) {
      column-count: var(--masonry-gallery-column-count);
    }
  }

  .masonry-gallery {
    .container {
      max-width: 1400px;
    }
  }
  .gallery-item {
    img {
      width: 100%;
      filter: var(--masonry-advanced-image-controls);
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--masonry-gallery-image-overlay);
    }
  }

  .card-columns {
    .card {
      margin-bottom: $half-margin;
      padding: 0;
      border: none;
    }
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--masonry-gallery-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--masonry-gallery-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--masonry-gallery-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--masonry-gallery-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--masonry-gallery-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--masonry-gallery-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--masonry-gallery-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--masonry-gallery-subheading-custom-word-spacing);
      }
    }
  }
}
