section.page-section-gallery {
  padding: 0;
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--image-gallery-block-gradient-border),
    var(--image-gallery-padding-top),
    var(--image-gallery-padding-bottom)
  );

  .background-texture {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
  }

  .gallery-container {
    position: relative;
    padding-right: 0;
    padding-left: 0;
    .row {
      justify-content: center;
      @media (min-width: $break-medium) {
        justify-content: end;
      }
    }
    .gallery-link-container {
      margin-bottom: 2rem;
      display: flex;
      align-self: center;
      justify-content: center;
    }
  }

  .gallery-image-container {
    height: 325px;
    @media (min-width: $break-large) {
      height: var(--gallery-height);
    }
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--image-gallery-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--image-gallery-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--image-gallery-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--image-gallery-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--image-gallery-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--image-gallery-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--image-gallery-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--image-gallery-subheading-custom-word-spacing);
      }
    }
  }
}

.image-gallery {
  width: 100%;
  height: 100%;
  .gallery-image-container {
    a.fancybox-gallery-link {
      display: block;
    }
    .fancybox-gallery-link {
      width: 100%;
      height: 100%;
      max-height: 100%;
      position: relative;
      .gallery-item {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        img {
          height: 100%;
          width: 100%;
          transition: all 0.5s;
          object-fit: cover;
          filter: var(--advanced-image-controls);
          &:hover {
            transform: scale(1.2);
          }
        }
        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: var(--gallery-image-overlay);
        }
      }
    }
  }
}
