.accordion-block {
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--accordion-block-gradient-border),
    var(--accordion-padding-top),
    var(--accordion-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  #accordion {
    position: relative;
  }

  .accordion-content {
    margin-bottom: 2rem;
  }

  .accordion-heading-container {
    .row {
      &.align-heading-left {
        justify-content: flex-start;
        text-align: left;
      }
      &.align-heading-center {
        justify-content: center;
        text-align: center;
      }
      &.align-heading-right {
        justify-content: flex-end;
        text-align: right;
      }
    }
    h3 {
      margin-bottom: 1.5rem;
    }
  }

  .accordion-content-container {
    #accordion-block {
      padding: 0;
    }
    @media (min-width: $break-large) {
      .row {
        #accordion-block {
          padding: 0 1rem 0 0;
        }
        &.flip-columns {
          flex-direction: row-reverse;
          #accordion-block {
            padding: 0 0 0 1rem;
          }
        }
      }
      .flip-columns {
        .accordion {
          padding-left: 2rem;
          padding-right: 0;
        }
      }
      .accordion {
        padding-right: 2rem;
      }
    }
  }

  .accordion-item {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid var(--wp--preset--color--primary-text);
    margin-bottom: 1rem;

    &:last-of-type {
      border-radius: 0;
    }

    .accordion-button-container {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 1rem 0;
      background-color: transparent;
      border: none;
      transition: color 200ms ease-in-out;
      &:not(.collapsed) {
        h5 {
          color: var(--wp--preset--color--brand-color);
        }
        box-shadow: none;
        i {
          color: var(--wp--preset--color--brand-color);
          transform: rotate(180deg);
        }
      }
      &.collapsed {
        &::after {
          background-image: none;
        }
      }
      &::after {
        background-image: none;
        margin-left: 0;
      }
      i {
        display: flex;
        align-self: flex-start;
        margin-top: 2px;
        font-size: 1.3rem;
        transition: transform 200ms ease-in-out;
        color: var(--wp--preset--color--primary-text);
      }
    }

    h5 {
      color: var(--wp--preset--color--primary-text);
      margin-bottom: 0;
      margin-right: 1.5rem;
      text-align: left;
    }
    .accordion-body {
      padding: 0.25rem 1.125rem 1rem 0;
    }
    .accordion-link {
      margin-bottom: 1rem;
    }
  }

  .accordion-image-container-column {
    display: none;
    @media (min-width: $break-large) {
      display: block;
      padding-top: 1rem;
    }
  }

  .accordion-image-container {
    min-height: var(--accordion-image-height);
    position: relative;
    &.has-image-overlay {
      &::after {
        content: "";
        position: absolute;
        background-color: var(--accordion-image-overlay-color);
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
      }
    }
  }

  .accordion-image {
    width: 100%;
    height: 100%;
    position: absolute;
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--accordion-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--accordion-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--accordion-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--accordion-heading-custom-word-spacing);
      }
    }
  }
  .accordion-header {
    h5 {
      &.has-font-size {
        font-size: var(--accordion-title-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--accordion-title-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--accordion-title-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--accordion-title-custom-word-spacing);
      }
    }
  }
}
