#content a:not(.btn-primary) {
  color: var(--wp--preset--color--brand-color);
}

#content .fa {
  color: var(--wp--preset--color--brand-color);
}

.page-section-header h2,
.card-title h2 {
  color: var(--wp--preset--color--brand-color);
}

.page-section-brand-color {
  background-color: var(--wp--preset--color--brand-color);
}

.page-section-brand-color .btn-primary {
  color: var(--wp--preset--color--brand-color);
  background-color: #fff;
}

.page-section-brand-color .btn-primary:hover {
  background-color: #f2f2f2;
}

.instructor-name {
  color: var(--wp--preset--color--brand-color);
}

.highlight-image:before {
  border-top: 2px solid var(--wp--preset--color--brand-color);
  border-left: 2px solid var(--wp--preset--color--brand-color);
}

.highlight-image .highlight-image-box:after {
  border-bottom: 2px solid var(--wp--preset--color--brand-color);
  border-right: 2px solid var(--wp--preset--color--brand-color);
}

.testimonial-container .carousel-nav-dots .active {
  background: var(--wp--preset--color--brand-color);
}
