.blog-gallery-card {
  @media (max-width: 992px) {
    margin-bottom: 3rem;
  }
}

.blog-gallery-content {
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding-bottom: 2rem;
  min-height: 650px;
  @media (max-width: 992px) {
    min-height: 600px;
  }
  .blog-gallery-image {
    width: 100%;
    height: 300px;
    margin-bottom: 2rem;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .blog-gallery-title {
    font-size: 1.25rem;
    margin-bottom: 1.5rem;
  }
  .blog-gallery-read-more {
    margin-top: auto;
  }
  .blog-gallery-title,
  .blog-gallery-excerpt {
    padding: 0 1.25rem;
  }
}
