@mixin strength-theme-table(
  $main-accent,
  $text-color,
  $background-color,
  $border-color,
  $strip-color
) {
  border: 2px solid var(--schedule-table-border-color);
  thead {
    tr {
      background-color: var(--schedule-table-background-color);
      border-bottom: 2px solid var(--schedule-table-border-color);
    }
    th {
      @media (min-width: $break-large) {
        border-right: 1px solid var(--schedule-table-border-color);
        &:last-of-type {
          border-right: none;
        }
      }
    }
  }
  tbody {
    tr {
      background-color: var(--schedule-table-background-color);
      @media (min-width: $break-large) {
        border-bottom: 1px solid var(--schedule-table-border-color);
      }
    }
    td {
      padding: 1rem;
      @media (min-width: $break-large) {
        border-right: 1px solid var(--schedule-table-border-color);
        &:last-of-type {
          border-right: none;
        }
      }
    }
  }
  th {
    font-size: 1.1rem;
    color: var(--schedule-table-text-color);
    text-align: center;
    border-right: 1px solid var(--schedule-table-stripe-color);
    background-color: var(--schedule-table-background-color);
    text-transform: uppercase;
    @media (min-width: $break-large) {
      font-size: 0.9rem;
    }
    &.today {
      background-color: var(--schedule-table-background-color);
      color: var(--wp--preset--color--brand-color);
      @media (min-width: $break-large) {
        font-size: 1rem;
        background-color: var(--wp--preset--color--brand-color);
        color: var(--schedule-table-background-color);
      }
    }
    &:last-of-type {
      border-left: none;
    }
    &.has-text-align-left {
      text-align: left;
    }
    &.has-text-align-center {
      text-align: center;
    }
    &.has-text-align-right {
      text-align: right;
    }
  }
  th.not-active {
    display: none;
    @media (min-width: $break-large) {
      display: table-cell;
    }
  }
  tr {
    &:nth-of-type(odd) {
      background-color: var(--schedule-table-stripe-color);
    }
  }
  td {
    font-size: 1rem;
    color: var(--schedule-table-text-color);
    text-align: center;
    border-right: 1px solid var(--schedule-table-stripe-color);
    @media (min-width: $break-large) {
      font-size: 0.8rem;
    }
    &:last-of-type {
      border-left: none;
    }
    &.not-active {
      display: none;
      @media (min-width: $break-large) {
        display: table-cell;
      }
    }
    &.empty-cell {
      display: none;
      @media (min-width: $break-large) {
        display: table-cell;
      }
    }
    &.has-text-align-left {
      text-align: left;
    }
    &.has-text-align-center {
      text-align: center;
    }
    &.has-text-align-right {
      text-align: right;
    }
  }
}
