.card-carousel {
  background-color: var(--wp--preset--color--base);
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--card-carousel-block-gradient-border),
    var(--card-carousel-padding-top),
    var(--card-carousel-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .carousel-heading {
    position: relative;
  }

  .carousel-container {
    position: relative;
  }

  // Carousel Tab Color Overrides
  .carousel-inner {
    &.has-carousel-tab-text-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            color: var(--carousel-tab-text-color);
          }
        }
      }
    }
    &.has-carousel-tab-background-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            background-color: var(--carousel-tab-background-color);
          }
        }
      }
    }
    &.has-carousel-tab-border-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            border: var(--carousel-tab-border-style);
          }
        }
      }
    }
    &.has-carousel-tab-text-color-background-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            color: var(--carousel-tab-text-color);
            background-color: var(--carousel-tab-background-color);
          }
        }
      }
    }
    &.has-carousel-tab-text-color-border-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            color: var(--carousel-tab-text-color);
            border: var(--carousel-tab-border-style);
          }
        }
      }
    }
    &.has-carousel-tab-background-color-border-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            background-color: var(--carousel-tab-background-color);
            border: var(--carousel-tab-border-style);
          }
        }
      }
    }
    &.has-carousel-tab-text-color-background-color-border-color {
      .carousel-item-card {
        &:not(.active) {
          .carousel-item-wrapper {
            color: var(--carousel-tab-text-color);
            background-color: var(--carousel-tab-background-color);
            border: var(--carousel-tab-border-style);
          }
        }
      }
    }
  }

  // Carousel Active Tab Color Overrides
  .carousel-inner {
    &.has-carousel-active-tab-text-color {
      .carousel-item-card.active .carousel-item-wrapper {
        color: var(--carousel-active-tab-text-color);
      }
    }
    &.has-carousel-active-tab-background-color {
      .carousel-item-card.active .carousel-item-wrapper {
        background-color: var(--carousel-active-tab-background-color);
      }
    }
    &.has-carousel-active-tab-border-color {
      .carousel-item-card.active .carousel-item-wrapper {
        border: var(--carousel-active-tab-border-style);
      }
    }
    &.has-carousel-active-tab-text-color-background-color {
      .carousel-item-card.active .carousel-item-wrapper {
        color: var(--carousel-active-tab-text-color);
        background-color: var(--carousel-active-tab-background-color);
      }
    }
    &.has-carousel-active-tab-text-color-border-color {
      .carousel-item-card.active .carousel-item-wrapper {
        color: var(--carousel-active-tab-text-color);
        border: var(--carousel-active-tab-border-style);
      }
    }
    &.has-carousel-active-tab-background-color-border-color {
      .carousel-item-card.active .carousel-item-wrapper {
        background-color: var(--carousel-active-tab-background-color);
        border: var(--carousel-active-tab-border-style);
      }
    }
    &.has-carousel-active-tab-text-color-background-color-border-color {
      .carousel-item-card.active .carousel-item-wrapper {
        color: var(--carousel-active-tab-text-color);
        background-color: var(--carousel-active-tab-background-color);
        border: var(--carousel-active-tab-border-style);
      }
    }
  }

  .carousel-card-container {
    position: relative;
    min-height: 400px;
  }

  .carousel-card {
    visibility: hidden;
    opacity: 0;
    padding-right: inherit;
    padding-left: inherit;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    animation: fadeInstructorCardOut 400ms ease;
    &.active {
      visibility: visible;
      position: relative;
      opacity: 1;
      padding-right: 0;
      padding-left: 0;
      animation: fadeInstructorCard 400ms ease;
    }
    &.blog-posts {
      .carousel-card-content-container {
        min-height: 350px;
        padding: 2rem;
        .carousel-card-content {
          padding: 0;
          margin-bottom: 1rem;
        }
      }
    }
  }
  .carousel-card-expand-content {
    display: block;
    margin-bottom: 1rem;
    @media (min-width: $break-medium) {
      display: none;
    }
    a {
      width: 100%;
      padding: 0.5rem 1rem;
      background-color: var(--wp--preset--color--primary-text);
      color: var(--wp--preset--color--base);
      text-decoration: none;
      @media (min-width: $break-small) {
        width: auto;
      }
    }
  }

  .carousel-title {
    h3 {
      &.has-font-size {
        font-size: var(--card-carousel-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--card-carousel-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--card-carousel-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--card-carousel-heading-custom-word-spacing);
      }
    }
  }
}

@keyframes fadeInstructorCard {
  0% {
    opacity: 0;
    display: none;
  }

  10% {
    opacity: 0.1;
    display: flex;
  }

  50% {
    opacity: 0.5;
    display: flex;
  }

  100% {
    opacity: 1;
    display: flex;
  }
}

@keyframes fadeInstructorCardOut {
  0% {
    opacity: 1;
    display: flex;
  }

  50% {
    opacity: 0.5;
    display: flex;
  }

  90% {
    opacity: 0.1;
    display: flex;
  }

  100% {
    opacity: 0;
    display: none;
  }
}

.carousel-heading {
  h3 {
    margin-bottom: 1rem;
  }
  .carousel-content {
    width: 100%;
    @media (min-width: $break-large) {
      width: 70%;
    }
  }
}

.carousel-container {
  margin-top: $theme-margin;
}

.carousel-container,
.instructor-container {
  @media (max-width: $break-medium) {
    padding-right: 1.5rem;
    padding-left: 1.5rem;
  }
}

.carousel-multi-item {
  .carousel-outer {
    position: relative;
  }

  .carousel-indicators {
    position: relative;
    margin-top: $half-margin;
    margin-right: 0;
    margin-left: 0;
    button {
      background-color: var(--wp--preset--color--primary-text);
      &.active {
        background-color: var(--wp--preset--color--brand-color);
      }
    }
  }
  .carousel-indicators [data-bs-target] {
    width: 15px;
    height: 15px;
    margin-right: 7px;
    margin-left: 7px;
  }

  .carousel-inner {
    padding: 0 1rem;
  }

  .carousel-item-next,
  .carousel-item-prev,
  .carousel-item.active {
    display: flex;
  }

  .carousel-control-next {
    display: flex;
    justify-content: end;
  }

  .carousel-control-prev {
    display: flex;
    justify-content: start;
  }

  .carousel-item-card {
    padding: 0 0.5rem;
    &.active {
      .carousel-item-wrapper {
        background-color: var(--wp--preset--color--brand-color);
        color: var(--wp--preset--color--primary-text);
      }
    }
    &:hover {
      cursor: pointer;
      filter: brightness(1.2);
    }
    .carousel-item-wrapper {
      padding: 0.5rem 1rem;
      background-color: var(--wp--preset--color--base-2);
      font-size: 1rem;
      text-align: center;
      transition: background-color 200ms ease-in-out;
      @media (min-width: $break-large) {
        font-size: 0.8rem;
        text-align: left;
      }
    }
    .carousel-item-name {
      font-weight: bold;
      line-height: 1.5rem;
    }
  }

  .carousel-inner {
    .carousel-item {
      transition: transform 0.6s ease-in-out;
    }

    .carousel-item-end.active,
    .carousel-item-next {
      transform: translateX(100%);
      @media (min-width: $break-medium) {
        transform: translateX(50%);
      }
      @media (min-width: $break-xLarge) {
        transform: translateX(33.3%);
      }
    }
    .carousel-item-start.active,
    .carousel-item-prev {
      transform: translateX(-100%);
      @media (min-width: $break-medium) {
        transform: translateX(-50%);
      }
      @media (min-width: $break-xLarge) {
        transform: translateX(-33.3%);
      }
    }
  }

  .carousel-inner {
    .carousel-item-end,
    .carousel-item-start {
      transform: translateX(0);
    }
  }

  .carousel-control-prev,
  .carousel-control-next {
    width: 5%;
    svg {
      color: var(--wp--preset--color--primary-text);
      height: 30px;
    }
  }
}
