section.blank-template {
  // Block Gradient Styles
  @include block-gradient-border(
    var(--blank-template-block-gradient-border),
    var(--blank-template-padding-top),
    var(--blank-template-padding-bottom)
  );

  .blank-template-container {
    padding: 0;
    .page-section-header {
      h2 {
        margin-bottom: 3rem;
      }
    }
    .blank-template-repeater {
      max-width: 100%;
      width: 100%;
      &:nth-child(odd) {
        flex-direction: row-reverse;
        .image-container {
          @media (min-width: $break-large) {
            padding: 3rem 5rem 3rem 3rem;
            margin: 0 auto 0 0;
          }
        }
        .content-container {
          &.no-media-content {
            margin: auto 0;
            padding: 3rem;
            @media (min-width: $break-large) {
              margin: 0 50% 0 0;
              padding: 3rem 3rem 3rem 5rem;
            }
          }
          @media (min-width: $break-large) {
            padding: 3rem 3rem 3rem 5rem;
            margin: 0 0 0 auto;
          }
        }
      }
      &:nth-child(even) {
        flex-direction: row;
        .image-container {
          @media (min-width: $break-large) {
            padding: 3rem 3rem 3rem 5rem;
            margin: 0 0 0 auto;
          }
        }
        .content-container {
          &.no-media-content {
            margin: auto 0;
            padding: 3rem;
            @media (min-width: $break-large) {
              margin: 0 0 0 50%;
              padding: 3rem 5rem 3rem 3rem;
            }
          }
          @media (min-width: $break-large) {
            padding: 3rem 5rem 3rem 3rem;
            margin: 0 auto 0 0;
          }
        }
      }
      .image-container,
      .content-container {
        max-width: 700px;
        margin: auto;
      }
      .image-container {
        padding: 3rem 3rem 1.5rem 3rem;
      }
      .content-container {
        padding: 1.5rem 3rem 3rem 3rem;
      }

      &.background-texture {
        position: relative;
        .repeater-background-texture {
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 0;
        }
      }
      .blank-template-media,
      .blank-template-content {
        position: relative;
        z-index: 1;
        height: auto;
        @media (min-width: $break-large) {
          height: 100%;
        }
      }
      .blank-template-media {
        overflow: hidden;
        img {
          position: relative;
          width: 100%;
          height: 100%;
          max-width: 100%;
          object-fit: cover;
          @media (min-width: $break-large) {
            position: absolute;
          }
        }
        img,
        video,
        iframe {
          align-self: center;
        }
        video {
          object-fit: cover;
        }
      }
    }
    .blank-template-video {
      max-width: 100%;
    }
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--blank-template-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--blank-template-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--blank-template-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--blank-template-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--blank-template-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--blank-template-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--blank-template-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--blank-template-subheading-custom-word-spacing);
      }
    }
  }
}
