section.instructors {
  text-align: center;
  padding: 0;
  position: relative;

  @include block-gradient-border(
    var(--instructors-block-gradient-border),
    var(--instructors-padding-top),
    var(--instructors-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .instructors-content-container {
    position: relative;
  }

  .instructors-content,
  .instructors-container {
    margin: 0 auto;
    @media (min-width: $break-small) {
      max-width: 550px;
    }
    @media (min-width: $break-large) {
      max-width: 996px;
    }
    @media (min-width: $break-xlarge) {
      max-width: 1200px;
    }
    @media (min-width: $break-xxl) {
      max-width: 1400px;
    }
  }

  // Main heading styles
  .instructors-content {
    margin-bottom: 2rem;
  }

  .page-section-instructor-content {
    margin-bottom: 2rem;
  }

  .instructor-description {
    max-width: 1000px;
    margin: 0 auto;
  }

  // Card Styles
  .instructors-container {
    justify-content: center;
    .card-gradient-border {
      height: 100%;
      background-image: var(--instructors-card-gradient-border);
      padding: var(--instructors-card-gradient-border-size);
      .instructor-container {
        height: 100%;
        background-color: var(--wp--preset--color--off-white);
        padding: 2rem 0 1rem;
      }
    }
  }

  .instructor {
    margin-bottom: 2rem;
  }

  .instructor-container {
    height: 100%;
    padding: 1.5rem 0;
  }

  .instructor-content {
    display: flex;
    flex-direction: column;
    .instructor-link-container {
      margin: auto 0 1rem;
      padding: 0.5rem 1.5rem 0;
    }
  }

  .instructor.one-column {
    &:nth-child(odd) {
      @media (min-width: $break-large) {
        .instructor-container {
          flex-direction: row;
          .instructor-content {
            margin-right: 4rem;
          }
        }
      }
    }
    &:nth-child(even) {
      @media (min-width: $break-large) {
        .instructor-container {
          flex-direction: row-reverse;
          .instructor-content {
            margin-left: 4rem;
          }
        }
      }
    }
    .instructor-container {
      display: flex;
      flex-direction: column;
      padding: 0;
    }
    .instructor-image {
      width: 100%;
      min-height: 325px;
      margin: 0;
      position: relative;
      @media (min-width: $break-large) {
        width: 35%;
      }
      img {
        position: absolute;
        left: 0;
        border-radius: 0;
      }
    }
    .instructor-content {
      width: 100%;
      margin: 0;
      padding: 2rem 1rem;
      @media (min-width: $break-large) {
        width: 65%;
        margin: 2rem;
        padding: 0 1rem;
      }
      .instructor-info,
      .instructor-bio {
        padding: 1rem 0 0;
      }
    }
  }

  .instructor.two-column {
    .instructor-image {
      width: 300px;
      height: 300px;
    }
    .instructor-content {
      height: calc(100% - (300px + 1rem));
    }
  }

  .instructor.three-column {
    .instructor-image {
      width: 250px;
      height: 250px;
    }
    .instructor-content {
      height: calc(100% - (250px + 1rem));
    }
  }

  .instructor-image {
    margin: 0 auto 1rem;

    img {
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .instructor-info {
    padding: 1rem 1.5rem 0;
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
    &.has-instructor-name-color {
      h4 {
        color: var(--instructor-card-name-color);
      }
    }
    &.has-instructor-title-color {
      h6 {
        color: var(--instructor-card-title-color);
      }
    }
  }

  .instructor-bio {
    padding: 0.5rem 1.5rem 0;
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--instructors-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--instructors-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--instructors-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--instructors-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--instructors-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--instructors-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--instructors-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--instructors-subheading-custom-word-spacing);
      }
    }
  }
  .instructor-info {
    h5 {
      &.has-font-size {
        font-size: var(--instructors-name-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--instructors-name-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--instructors-name-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--instructors-name-heading-custom-word-spacing);
      }
    }
    h6 {
      &.has-font-size {
        font-size: var(--instructors-title-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--instructors-title-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--instructors-title-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--instructors-title-heading-custom-word-spacing);
      }
    }
  }
}
