section.page-section-features {
  position: relative;
  &.page-section {
    padding: 0;
  }

  // Block Gradient Styles
  @include block-gradient-border(
    var(--features-block-gradient-border),
    var(--features-padding-top),
    var(--features-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .features-content {
    width: 100%;
    margin-bottom: $theme-margin;
    @media (min-width: $break-large) {
      width: 70%;
    }
  }

  .feature-container {
    &.alternating-background-color {
      margin: 0;
      padding: 2rem 0;
      background-color: var(--second-features-card-background);
      .accordion-body {
        @media (max-width: $break-large) {
          &.no-heading-fill-color {
            padding-top: $half-padding;
          }
        }
      }
    }
    &.alternating-background-color.card-column-6 {
      @media (max-width: $break-large) {
        &:nth-child(2n + 1) {
          background-color: var(--first-features-card-background);
        }
      }
      @media (min-width: $break-large) {
        &:nth-child(4n + 1) {
          background-color: var(--first-features-card-background);
        }
        &:nth-child(4n + 4) {
          background-color: var(--first-features-card-background);
        }
        .accordion-item {
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding-right: 3rem;
        }
      }

      .feature-icon {
        padding: 0.5rem 0.5rem 0.5rem $half-padding;
        @media (min-width: $break-large) {
          padding: $half-padding 0.5rem $half-padding $double-padding;
        }
      }
    }
    &.alternating-background-color.card-column-4 {
      &:nth-child(2n + 1) {
        background-color: var(--first-features-card-background);
      }
    }
    &.alternating-background-color.card-column-3 {
      @media (max-width: $break-large) {
        &:nth-child(2n + 1) {
          background-color: var(--first-features-card-background);
        }
      }
      @media (min-width: $break-large) {
        &:nth-child(5n + 1) {
          background-color: var(--first-features-card-background);
        }
        &:nth-child(5n + 3) {
          background-color: var(--first-features-card-background);
        }
      }
    }
  }

  #features {
    margin: 0 auto;
    position: relative;

    .col-12 {
      display: flex;
      .features-content {
        @media (min-width: $break-large) {
          max-width: 70%;
        }
      }
      &.align-heading-left {
        justify-content: start;
      }
      &.align-heading-center {
        justify-content: center;
        text-align: center;
      }
      &.align-heading-right {
        justify-content: end;
        text-align: right;
      }
    }

    .features-heading {
      margin-bottom: 1rem;
    }

    .feature-container {
      display: flex;
      .accordion-body {
        padding: 1rem 1.25rem;
        @media (min-width: $break-large) {
          padding: 0 1.25rem 1rem;
        }
      }
    }

    .feature-icon-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .accordion-item {
      width: 100%;
      background-color: transparent;
      h5 {
        @media (max-width: $break-large) {
          display: flex;
        }
        button {
          color: var(--feature-heading-title-color);
          font-weight: inherit;
          font-size: inherit;
          text-transform: inherit;
          &:not(.collpased) {
            background-color: var(--feature-heading-background-color);
            box-shadow: none;
          }
          @media (max-width: $break-large) {
            display: flex;
            justify-content: space-between;
            padding-left: 0.5rem;
          }
          @media (min-width: $break-large) {
            display: flex;
            &.align-title-left {
              justify-content: flex-start;
            }
            &.align-title-center {
              justify-content: center;
            }
            &.align-title-right {
              justify-content: flex-end;
            }
            &:hover {
              cursor: default;
            }
          }
        }
      }

      p {
        color: var(--feature-heading-text-color);
      }

      .accordion-button::after {
        display: none;
      }

      .accordion-button {
        @media (min-width: $break-large) {
          padding: 1rem 1.25rem 0;
        }
        i {
          opacity: 0.5;
          transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
          @media (min-width: $break-large) {
            display: none;
          }
          &::before {
            font-size: 1.8rem;
          }
        }
        &:not(.collapsed) i {
          opacity: 1;
          transform: rotate(-180deg);
          color: var(--feature-heading-title-color);
        }
      }
    }
  }

  .feature-icon {
    padding: 0.5rem 0 0.5rem 1.25rem;
    @media (min-width: $break-large) {
      padding: $half-padding $theme-padding;
    }
    i {
      font-size: calc(var(--features-icon-size) / 2.5);
      color: var(--features-icon-fill-color);
      @media (min-width: $break-large) {
        font-size: var(--features-icon-size);
      }
    }
    svg {
      stroke: var(--features-icon-fill-color);
    }
    svg,
    img {
      width: auto;
      height: calc(var(--features-icon-size) / 2.25);
      @media (min-width: $break-large) {
        min-width: calc(var(--features-icon-size) * 1.25);
        height: var(--features-icon-size);
      }
    }
    &.vertical-card {
      display: flex;
      justify-content: center;
    }
  }
  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--features-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--features-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--features-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--features-heading-custom-word-spacing);
      }
    }
  }
  .accordion-item {
    h5 {
      &.has-font-size {
        font-size: var(--features-feature-title-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--features-feature-title-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--features-feature-title-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--features-feature-title-custom-word-spacing);
      }
    }
  }
}
