/**
 * *******************************************************************
 * Block Override Gradient Border - This mixin will add the necesary styles for 
 * a top, bottom, or top and bottom gradient border.
 * *******************************************************************
 */
@mixin block-override-gradient-border(
  $gradient-color,
  $padding-top,
  $padding-bottom
) {
  &.has-gradient-border-top,
  &.has-gradient-border-bottom,
  &.has-gradient-border-topAndBottom {
    .cta-block {
      border-image-source: $gradient-color;
      border-image-slice: 1;
    }
  }
  &.has-gradient-border-top {
    .cta-block {
      border-top: $padding-top solid;
      border-bottom: 0;
    }
  }
  &.has-gradient-border-bottom {
    .cta-block {
      border-bottom: $padding-bottom solid;
      border-top: 0;
    }
  }
  &.has-gradient-border-topAndBottom {
    .cta-block {
      border-top: $padding-top solid;
      border-bottom: $padding-bottom solid;
    }
  }
}
