section.page-section-content-block {
  position: relative;

  // Block Gradient Styles
  @include block-gradient-border(
    var(--content-block-block-gradient-border),
    var(--content-block-padding-top),
    var(--content-block-padding-bottom)
  );

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .content-block-container {
    position: relative;
    img {
      max-width: 100%;
      height: auto;
    }
  }

  .content-block-col {
    margin: 0.75rem 0;
    @media (min-width: $break-medium) {
      margin: 0;
    }
    .card-format {
      background-color: var(--content-block-card-background-color);
      padding: 20px;
      @media (min-width: $break-medium) {
        padding: 2rem;
      }
    }
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--content-block-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--content-block-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--content-block-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--content-block-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--content-block-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--content-block-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--content-block-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--content-block-subheading-custom-word-spacing);
      }
    }
  }
}
