.floating-bar-container {
  position: fixed;
  width: 100%;
  z-index: 1001;
  top: auto;
  bottom: 0;
  @media (min-width: $break-large) {
    position: sticky;
    top: 0;
    bottom: auto;
  }
}

#floating-bar {
  padding: 0.5rem 0;
  display: grid;
  width: 100%;
  font-size: 0.8rem;
  @media (max-width: $break-large) {
    min-height: 50px;
  }
  @media (max-width: $break-small) {
    font-size: 0.7rem;
  }
  .social-list {
    a {
      color: inherit !important;
    }
  }

  @media (min-width: $break-large) {
    &.has-custom-drop-shadow {
      z-index: 1;
      box-shadow: var(--floating-bar-drop-shadow);
    }
  }
}

.floating-bar-header {
  display: grid;
  grid-template-columns: 25% 50% 25%;
  grid-template-areas: "padding-left bar-message bar-contact";
  height: 100%;
  align-items: center;
  @media (max-width: $break-small) {
    &.two-links {
      grid-template-columns: 5% 60% 35%;
    }
  }
}

#floating-bar-social-list {
  display: none;
  @media (min-width: $break-large) {
    display: flex;
    grid-column: 1/2;
    padding-left: 1rem;
    #floating-social-link {
      display: flex;
      font-size: 1rem;
      margin-right: 0.5rem;
      a {
        text-decoration: none;
      }
      .fa-brands {
        display: flex;
        justify-content: center;
        &:hover {
          filter: brightness(1.1);
        }
      }
    }
  }
}

#floating-bar-header-message {
  grid-column: 2/3;
  text-align: center;
  .floating-bar-button {
    width: 100%;
    background-color: transparent;
    border: none;
    color: inherit;
    font-weight: normal;
    text-decoration: none;
    @media (min-width: $break-small) {
      margin: 0 0.5rem;
      font-weight: bold;
    }
  }
  span {
    font-size: 1rem;
    @media (min-width: $break-large) {
      font-size: var(--floating-bar-message-font-size);
    }
  }
}

#floating-bar-header-links {
  grid-column: 3/4;
  display: flex;
  flex-direction: row;
  justify-content: end;
  padding-right: 1rem;
  align-items: center;
  @media (max-width: $break-large) {
    height: 40px;
  }
  ul {
    margin-top: 0;
    margin-bottom: 0;
    li {
      margin-right: 0.5rem;
      margin-left: 0.5rem;
    }
  }
  #floating-bar-phone-link {
    padding-left: 1rem;
  }
  #floating-bar-email-link {
    margin-left: 1rem;
  }

  #floating-bar-phone-link,
  #floating-bar-email-link {
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }

  @media (max-width: $break-large) {
    #floating-bar-social-list {
      display: none;
    }
    #floating-bar-phone-link {
      border-left: 0;
      padding-left: 0;
    }
  }

  @media (max-width: $break-xLarge) {
    #floating-bar-number {
      display: none;
    }
    #floating-bar-phone-link,
    #floating-bar-email-link {
      height: 100%;
      width: 100%;
      margin-left: 0.35rem;
      max-width: 50px;
      a {
        display: grid;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-items: center;
        &:hover {
          text-decoration: none;
        }
      }
      @media (max-width: $break-large) {
        border: 1px solid;
      }
    }
    .fa-phone,
    .fa-envelope {
      font-size: 1.25rem;
    }
    .fas,
    .fab {
      font-size: 1.25rem;
    }
  }
}
