#map {
  padding: 0;

  .acf-map {
    width: 100%;
    height: 100%;
    h3 {
      color: #424242 !important;
      letter-spacing: 0.2rem;
      font-size: 1.2rem;
      text-transform: none;
    }
    p {
      color: #424242 !important;
    }
    img {
      max-width: inherit !important;
    }
  }

  .map-container {
    h1,
    h3 {
      text-transform: uppercase;
    }
    .inactive-school {
      display: none;
    }
  }

  .map-content {
    @media (min-width: $break-large) {
      padding: 8rem;
    }
    /* Landon */
    /* Makes text above map content form look better on mobile */
    @media (max-width: $break-large) {
      padding: 2rem 4rem;
    }

    .school-location-label {
      font-weight: bold;
    }

    #school-location {
      background-color: #fff;
      color: #2b2b2b;
      padding: 5px;
    }

    iframe {
      width: 100%;
      border: none;
    }
    /**
    * This code is to help make the iframe container evenly aligned to 
    * the left with the rest of the map content.
    */
    .map-lead-form-container {
      transform: translateX(-40px);
      width: calc(100% + 80px);
      max-width: 767px;
    }
  }

  .full-width {
    .acf-map {
      height: 500px;
    }
  }

  .row-map {
    height: 500px;

    @media (min-width: $break-large) {
      height: 100%;
    }
  }

  .map-row-padding {
    /* Travis */
    /* This adjusts for the bootstrap padding added to the col-lg-6 */
    padding-left: 0px;
    padding-right: 0px;
  }
}
