section.cta {
  position: relative;
  padding: 0;
  text-align: center;

  // Block Gradient CPT Styles
  .cta-block {
    @include block-gradient-border(
      var(--cta-cpt-block-gradient-border),
      var(--cta-cpt-padding-top),
      var(--cta-cpt-padding-bottom)
    );
  }
  &.has-block-level-gradient-border {
    // Block Gradient Styles
    @include block-override-gradient-border(
      var(--cta-block-gradient-border),
      var(--cta-padding-top),
      var(--cta-padding-bottom)
    );
  }

  // Block Solid CPT Styles
  .cta-block {
    @include block-solid-border(
      var(--cta-cpt-solid-border-top),
      var(--cta-cpt-solid-border-bottom)
    );
  }
  &.has-block-level-solid-border {
    // Block Solid Styles
    @include block-override-solid-border(
      var(--cta-solid-border-top),
      var(--cta-solid-border-bottom)
    );
  }

  @media (min-width: 578px) {
    text-align: left;
  }
}

.cta-block {
  position: relative;

  .background-texture {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }

  .cta-container {
    padding-top: 2rem;
    padding-bottom: 2rem;
    position: relative;
  }

  .cta-link-container {
    display: flex;
    justify-content: center;
    align-self: start;
    .btn-cta,
    .cta-button {
      transition: all 0.15s ease-in-out;
    }

    .wp-element-button,
    .wp-element-button:not(:disabled):not(.disabled) {
      &.has-text-color {
        color: var(--cta-cpt-button-text-color);
      }
      &.has-bg-color {
        background-color: var(--cta-cpt-button-bg-color);
      }
      &.has-border-color {
        border: var(--cta-cpt-button-border-color);
      }
      &:hover,
      &:active {
        &.has-text-color-hover {
          color: var(--cta-cpt-button-hover-text-color);
        }
        &.has-bg-color-hover {
          background-color: var(--cta-cpt-button-hover-bg-color);
        }
        &.has-border-color-hover {
          border: var(--cta-cpt-button-hover-border-color);
        }
      }
    }
  }
}
