section.schedule-table {
  position: relative;

  // Block Border Options Mixin found "/src/scss/mixins/gradient-border.scss"
  @include block-gradient-border(
    var(--schedule-table-block-gradient-border),
    var(--schedule-table-padding-top),
    var(--schedule-table-padding-bottom)
  );

  .background-texture {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
  }

  .schedule-table-container {
    position: relative;

    .schedule-loading-screen {
      display: flex;
      justify-content: center;
      align-items: center;
      visibility: visible;
      opacity: 1;
      &.disabled {
        visibility: hidden;
        position: absolute;
        opacity: 0;
      }
    }
  }

  @media (max-width: $break-large) {
    #schedule {
      width: 100%;
      &.hidden {
        height: 550px;
        visibility: hidden;
        opacity: 0;
        transition: opacity 300ms ease-in;
      }
      &.visible {
        height: auto;
        visibility: visible;
        opacity: 1;
        transition: opacity 300ms ease-in;
      }
    }
  }

  .schedule-table-mobile-view-select {
    display: block;
    @media (min-width: $break-large) {
      display: none;
    }
    label {
      margin-bottom: 0.5rem;
    }
  }

  #schedule-table-select {
    color: #f2f2f2;
    background-color: var(--wp--preset--color--brand-color);
    border-radius: 0;
    border: 2px solid var(--wp--preset--color--brand-color);
  }

  .schedule-content-container {
    max-width: 900px;
    margin: 0 auto $theme-margin;
  }

  .schedule-filter-container {
    display: none;
    @media (min-width: $break-large) {
      display: block;
    }
    span {
      display: flex;
      justify-content: space-between;
    }
  }

  #schedule-clear-filters {
    border: none;
    background-color: var(--wp--preset--color--brand-color);
    color: #f2f2f2;
    font-size: 0.8rem;
    padding: 5px 10px;
  }

  .schedule-program-name-filter {
    margin: $half-margin 0 $theme-margin;
    .schedule-filter-button {
      background: transparent;
      border: 2px solid var(--wp--preset--color--brand-color);
      margin-right: $half-margin;
      margin-bottom: 0.5rem;
      padding: 5px 10px;
      font-size: 0.8rem;
      color: var(--schedule-table-text-color);
      &:hover {
        background-color: var(--wp--preset--color--brand-color);
        color: #f2f2f2;
      }
      &:focus {
        background-color: var(--wp--preset--color--brand-color);
        color: #f2f2f2;
      }
    }
  }

  .table {
    background-color: var(--schedule-table-background-color);
    // Table mixin found in "/src/scss/mixins/table.scss"
    @include strength-theme-table(
      var(--wp--preset--color--brand-color),
      var(--schedule-table-text-color),
      var(--schedule-table-background-color),
      var(--schedule-table-border-color),
      var(--schedule-table-stripe-color)
    );
  }

  .schedule-link {
    display: flex;
    justify-content: center;
    @media (min-width: $break-large) {
      justify-content: right;
    }
  }

  .page-section-header {
    h3 {
      &.has-font-size {
        font-size: var(--schedule-table-heading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--schedule-table-heading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--schedule-table-heading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--schedule-table-heading-custom-word-spacing);
      }
    }
    h4 {
      &.has-font-size {
        font-size: var(--schedule-table-subheading-custom-font-size);
      }
      &.has-line-height {
        line-height: var(--schedule-table-subheading-custom-line-height);
      }
      &.has-letter-spacing {
        letter-spacing: var(--schedule-table-subheading-custom-letter-spacing);
      }
      &.has-word-spacing {
        word-spacing: var(--schedule-table-subheading-custom-word-spacing);
      }
    }
  }
}
