// Page Header
// --------------------------------------------------

a[href^="tel:"] {
  cursor: pointer !important;
}

main {
  margin: 0;
}

.site-header {
  width: 100%;
}

.no-gutters {
  margin-right: 0;
  margin-left: 0;

  > .col,
  > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

.row-eq-height {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  /* Landon */
  /* This helps get rid of that god damn -15px margins that some god damn idiot put in this god damn theme */
  margin-right: 0;
  margin-left: 0;

  &.no-gutters > [class*="col"] {
    padding: 0 !important;
  }
}

.row-eq-height > [class*="col"] {
  display: flex;
  flex-direction: column;
  padding: 0;

  @media (min-width: $break-small) {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.row-eq-height > [class*="col"] > div {
  height: 100%;
}

/* Landon */
// This makes any embedded videos fill to fit their container
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

button {
  &:focus {
    outline: 0;
  }
}

// Overriding Safari default button styling on anchor tag with specific attributes
body {
  [type="button"],
  [type="reset"],
  [type="submit"],
  button {
    -webkit-appearance: none;
    appearance: none;
  }
}

