.lead-capture-loader,
.schedule-mobile-loader {
  width: 75px;
  height: 75px;
  border: 8px solid #f2f2f2bb;
  border-top-color: #606060;
  border-radius: 50%;
}

.start-spin {
  animation: 750ms linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
